import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../services/data.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {
  addSubAdminForm: FormGroup;
  id: any;
  user: any;
  permission: any;
  constructor(public router: Router, public service: DataService, public activatedRoute: ActivatedRoute) {


  }

  ngOnInit() {
    this.getId();
    this.search();

    this.addSubAdminForm = new FormGroup({
      'fullName': new FormControl('', Validators.compose([Validators.required, Validators.pattern(/^[^\s][a-zA-Z ]*$/)])),
      "password": new FormControl('', ([Validators.required, Validators.minLength(8), Validators.pattern(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,16}$/)])),
      "confirmPassword": new FormControl('', ([Validators.required, Validators.minLength(8)])),
      "email": new FormControl('', ([Validators.required, Validators.pattern(/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,3})$/), Validators.maxLength(50)])),
      "phoneNumber": new FormControl('', ([Validators.required, Validators.maxLength(13), Validators.pattern(/^[1-9][0-9]{9,13}$/)])),
      'dashboard': new FormControl(''),
      'userManagement': new FormControl(''),
      'bannerManagement': new FormControl(''),
      'auctionCategoryManagement': new FormControl(''),
      'categoryManagement': new FormControl(''),
      'orderManagement': new FormControl(''),
      'jobManagement': new FormControl(''),
      'groupManagement': new FormControl(''),
      'videoGameManagement': new FormControl(''),
      'advertiseManagement': new FormControl(''),
      'nonProfitManagement': new FormControl(''),
      'eventManagement': new FormControl(''),
      'classRoomManagement': new FormControl(''),
      'discussionForumManagement': new FormControl(''),
      'newsManagement': new FormControl(''),
      'auctionSubCategoryManagement': new FormControl(''),
      'paymentManagement': new FormControl(''),
      'buyingSellingTransactionManagement': new FormControl(''),
      'faqManagement': new FormControl(''),
      'reportManagement': new FormControl(''),
      'staticContentManagement': new FormControl(''),
      'auctionProductManagement' : new FormControl(''),
      'subCategoryManagement' : new FormControl(''),
      'buyingSellingProductManagement' : new FormControl(''),
      'stickerManagement' : new FormControl(''),
      'gifManagement' : new FormControl(''),
      'industryManagement' : new FormControl(''),
      'jobTransactionManagement' : new FormControl(''),
      'pageManagement' :  new FormControl(''),
      'auctionTransactionManagement' : new FormControl(''),
      'generalReportManagement' : new FormControl(''),
      'auctionOrderManagement' : new FormControl('')


    })
  }
  getId() {
    this.activatedRoute.params.subscribe(params => {
      this.id = params.id
      // console.log(">>>>>>>>>>>>>>>>>>>>",this.id,"KKKKKKKKKK",params)
    })
  }

  inputKeyPress(e){
    var k
    document.all ? k = e.keyCode : k = e.which;
    return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
  }
  editUser() {

    if ((this.addSubAdminForm.controls['confirmPassword'].value != this.addSubAdminForm.controls['password'].value) && this.addSubAdminForm.invalid) {
      return this.service.toastErr('please enter valid details')
    }


    let addData = {
      subAdminId: this.id,
      adminId: JSON.parse(localStorage.getItem('adminId')),
      "userType": "SUBADMIN",
      "fullName": this.addSubAdminForm.value.fullName,
      "email": this.addSubAdminForm.value.email,
      "password": this.addSubAdminForm.value.password,
      "phoneNumber": this.addSubAdminForm.value.phoneNumber,
      "confirmPassword": this.addSubAdminForm.value.confirmPassword,
      "dashboard": this.addSubAdminForm.value.dashboard,
      "userManagement": this.addSubAdminForm.value.userManagement,
      "bannerManagement": this.addSubAdminForm.value.bannerManagement,
      "auctionCategoryManagement": this.addSubAdminForm.value.auctionCategoryManagement,
      "categoryManagement": this.addSubAdminForm.value.categoryManagement,
      "orderManagement": this.addSubAdminForm.value.orderManagement,
      "jobManagement": this.addSubAdminForm.value.jobManagement,
      "groupManagement": this.addSubAdminForm.value.groupManagement,
      "videoGameManagement": this.addSubAdminForm.value.videoGameManagement,
      "advertiseManagement": this.addSubAdminForm.value.advertiseManagement,
      "nonProfitManagement": this.addSubAdminForm.value.nonProfitManagement,
      "eventManagement": this.addSubAdminForm.value.eventManagement,
      "classRoomManagement": this.addSubAdminForm.value.classRoomManagement,
      "discussionForumManagement": this.addSubAdminForm.value.discussionForumManagement,
      "newsManagement": this.addSubAdminForm.value.newsManagement,
      "auctionSubCategoryManagement": this.addSubAdminForm.value.auctionSubCategoryManagement,
      "paymentManagement": this.addSubAdminForm.value.paymentManagement,
      "buyingSellingTransactionManagement": this.addSubAdminForm.value.buyingSellingTransactionManagement,
      "faqManagement": this.addSubAdminForm.value.faqManagement,
      "reportManagement": this.addSubAdminForm.value.reportManagement,
      "staticContentManagement": this.addSubAdminForm.value.staticContentManagement,
      "auctionProductManagement" :  this.addSubAdminForm.value.auctionProductManagement,
      "subCategoryManagement" : this.addSubAdminForm.value.subCategoryManagement,
      "buyingSellingProductManagement" : this.addSubAdminForm.value.buyingSellingProductManagement,
      "stickerManagement" : this.addSubAdminForm.value.stickerManagement,
      "gifManagement" : this.addSubAdminForm.value.gifManagement,
      "industryManagement" : this.addSubAdminForm.value.industryManagement,
      "jobTransactionManagement" :  this.addSubAdminForm.value.jobTransactionManagement,
      "pageManagement" : this.addSubAdminForm.value.pageManagement,
      "auctionTransactionManagement" : this.addSubAdminForm.value.auctionTransactionManagement,
      "generalReportManagement" : this.addSubAdminForm.value.generalReportManagement,
      "auctionOrderManagement" : this.addSubAdminForm.value.auctionOrderManagement


    }
    console.log("hello", this.addSubAdminForm.value, "in req data", addData);
    console.log("updated value", addData.dashboard);
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/editSudAdmin', addData, '').subscribe(response => {
      if (response['responseCode'] == 200) {
        setTimeout(() => {
          this.service.hideSpinner()

        }, 1000)
        this.service.showSuccess(response['responseMessage'])
        this.user = response['result']['permission']


        console.log("hhhhhhhhhhhhhhhhh", this.user)
        this.router.navigate(['sub-adminmanagement'])
      }
      else {
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      console.log(">>>>>>>>>>>", error)
      this.service.toastErr('Something Went Wrong')
    })
  }

  search() {
    let postData = {
      subAdminId: this.id,
      adminId: JSON.parse(localStorage.getItem('adminId'))

    }
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewSubAdmin`, postData, '').subscribe((res: any) => {
      if (res['responseCode'] == 200) {
        this.service.hideSpinner()
        this.user = res['result'];
        this.permission = res['result']['permission'];
        console.log("to check userDetails", this.user)
        this.addSubAdminForm.patchValue({
          fullName: res.result.firstName,
          email: res.result.email,
          phoneNumber : res.result.phoneNumber,
          advertiseManagement : res.result.permission.advertiseManagement,
          auctionCategoryManagement : res.result.permission.auctionCategoryManagement,
          bannerManagement : res.result.permission.bannerManagement,
          categoryManagement : res.result.permission.categoryManagement,
          classRoomManagement : res.result.permission.classRoomManagement,
          dashboard : res.result.permission.dashboard,
          discussionForumManagement : res.result.permission.discussionForumManagement,
          eventManagement : res.result.permission.eventManagement,
          faqManagement : res.result.permission.faqManagement,
          groupManagement : res.result.permission.groupManagement,
          jobManagement : res.result.permission.jobManagement,
          auctionSubCategoryManagement : res.result.permission.auctionSubCategoryManagement,
          newsManagement : res.result.permission.newsManagement,
          nonProfitManagement : res.result.permission.nonProfitManagement,
          orderManagement : res.result.permission.orderManagement,
          paymentManagement : res.result.permission.paymentManagement,
          reportManagement : res.result.permission.reportManagement,
          staticContentManagement : res.result.permission.staticContentManagement,
          buyingSellingTransactionManagement : res.result.permission.buyingSellingTransactionManagement,
          userManagement : res.result.permission.userManagement,
          videoGameManagement : res.result.permission.videoGameManagement,
          auctionProductManagement : res.result.permission.auctionProductManagement,
          subCategoryManagement : res.result.permission.subCategoryManagement,
          buyingSellingProductManagement : res.result.permission.buyingSellingProductManagement,
          stickerManagement : res.result.permission.stickerManagement,
          gifManagement : res.result.permission.gifManagement,
          industryManagement : res.result.permission.industryManagement,
          jobTransactionManagement : res.result.permission.jobTransactionManagement,
          pageManagement : res.result.permission.pageManagement,
          auctionTransactionManagement : res.result.permission.auctionTransactionManagement,
          generalReportManagement : res.result.permission.generalReportManagement,
          auctionOrderManagement : res.result.permission.auctionOrderManagement
        })
      }
      else{
        this.service.hideSpinner()
      }
    },(err)=>{
      this.service.toastErr('Something went wrong');
      this.service.hideSpinner()
    })
  }

}
