import { Component, OnInit, Injectable } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare  var $ : any;

@Injectable({
  providedIn: 'root'
})
@Component({
  selector: 'app-buying-selling-category-management',
  templateUrl: './buying-selling-category-management.component.html',
  styleUrls: ['./buying-selling-category-management.component.css']
})
export class BuyingSellingCategoryManagementComponent implements OnInit {
  searchkey :any ="";
  user:any;
  form:FormGroup;
  Bool :boolean = false;
  pageNo: any = 1;
  paginationData: any;
  srNo: number;
  deleteId: any;
  status:any;
  userId: any;
  catId:any = [];
  limit:any;
  dummyData :any;
  total: any;
  Array: any = [];
  messege: any;
  Value:any;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;

  constructor(private service: DataService,private router : Router) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
   }

            ngOnInit() {
              this.form =  new FormGroup({
                "fromDate" : new FormControl(''),
                  "toDate" : new FormControl('')
              });
              this.service.BlockFuture()
              this.search(this.pageNo)
            }
                  
            search(page){
              this.pageNo = page
              let postData = {
                adminId : JSON.parse(localStorage.getItem('adminId')),
                pageNumber : this.pageNo,
                search : this.searchkey
              }
              this.service.postApi('api/v1/admin/viewCategory',postData,'').subscribe((res)=>{
                if(res['responseCode'] == 200){
                  this.user = res['result'];
                  this.Array = res['result']['docs']
                  this.Array.forEach(element => {
                    this.catId.push(element._id)
                  });
                //  console.log(this.catId)
                  // this.dummyData = res['result']['docs']
                  this.paginationData = res['result']
                  this.total=res['result']['total']
                  this.limit = res['result']['limit']
                  this.messege = res['responseMessage']
                  this.srNo = (this.pageNo - 1) * this.paginationData.limit
                }
                
                else{
                  this.service.toastErr(res['responseMessage'])
                  this.messege = res['responseMessage']
                }
              },
              (error)=>{
                this.service.toastErr('something went wrong')
                
              })

  }
  Reset(){
    this.searchkey= ''
    this.form.reset()
     this.searchByDate()
}    
 
  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.messege = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){
    
   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
    
    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewCategory',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.user = res['result'];
        this.messege = res['responseMessage']
      }
      else{
        this.messege = res['responseMessage']
 
      }
     
    })
  }
}
  
            view(id){
              console.log('this user id====>',id)
                this.router.navigate(['buying-view-category/'+id])
            }

            delete(id){
              this.userId = id
              $('#delete').modal('show');
            }
            deleteUser(){
              // this.delete(id)
              let postData = {
                adminId : JSON.parse(localStorage.getItem('adminId')),
                'categoryId' : this.userId,
                'status' : 'DELETE'
              }
              console.log("delete user id ===>",this.userId)
             this.service.postApi('api/v1/admin/categoryManagment',postData,'').subscribe((res)=>{
                  if(res['responseCode']==200){
                    this.service.showSuccess(res['responseMessage'])
                    $('#delete').modal('hide');
                    this.search(this.pageNo)
                  }
             }, (error)=>{
               this.service.toastErr('something went wrong')
             })
            }

          edit(id){
            console.log("Id=-=--=->",id);
            let user = this.Array.filter((item)=>item._id == id)
            console.log("User=--=-==--==-=->",user);  
              this.service.setCategory(user)
              this.router.navigate(['buying-edit-category/'+id])
              }

                  sliderRound(id,status)
                  {
                    this.status= status
                    console.log(id,status)
                    if(this.status=='ACTIVE'){
                   
                    this.status == "BLOCK"  
                    }
                    $('#blocked').modal('show')
                    console.log(id,status)
                    console.log(id,this.status)
                    this.userId = id;
                    // var Status = status
                    this.status = status;                              
                  }
                  blockCategory(id,status)
                  {
                    this.status= status
                    if (this.status=='BLOCK'){
                      this.status = "ACTIVE "
                    }
                    $('#activate').modal('show')

                    // console.log(id,status)
                    this.userId = id;
                    this.status = status;                              
                  }


                  Activate(){ 
                    this.sliderRound(this.userId,this.status)
                    console.log('this is sthe id and status',this.userId,this.status)
                    if(this.status=='ACTIVE'){
                      this.status = 'BLOCK';    
                      }
                      else{
                        this.status = 'ACTIVE';
                      }    
                    console.log(this.status)
                 
                 
                    let apireq = {
                      adminId : JSON.parse(localStorage.getItem('adminId')),
                      'categoryId':this.userId,
                      'status': this.status,
                      }
                      // console.log('apireq ==>>', apireq)
                    this.service.postApi('api/v1/admin/categoryManagment',apireq,'').subscribe((success:any)=> {
                      console.log(success)
                      if (success.responseCode === 200) {
                        $('#blocked').modal('hide')
                          this.service.toastErr(success['responseMessage'])                                         
                        this.search(this.pageNo)     
                         }
                  },error => {
                    console.log('Something went wrong');
                  })
                 
                }

                  Block(){
                    this.blockCategory(this.userId,this.status)
                    console.log('this is sthe id and status',this.userId,this.status)
                    if(this.status=='ACTIVE'){
                      this.status = 'BLOCK';    
                      }
                      else{
                        this.status = 'ACTIVE';
                      }    
                    console.log(this.status)
                    let req = {
                      adminId : JSON.parse(localStorage.getItem('adminId')),
                      'categoryId':this.userId,
                      'status': this.status,
                      }
                    this.service.postApi('api/v1/admin/categoryManagment',req,'').subscribe((success:any)=> {
                      console.log(success)
                      if (success.responseCode === 200) {
                        $('#activate').modal('hide')  
                          this.service.showSuccess(success['responseMessage'])                     
                        this.search(this.pageNo)     
                    }
                  },error => {
                    console.log('Something went wrong');
                  }) 
                  }


}
