import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {
form:FormGroup;
  status: boolean = false;
  constructor(private service : DataService,private router :Router) { }

  ngOnInit() {
    this.form = new FormGroup({
      "OldPassword" : new FormControl('',([Validators.required])),
      "NewPassword" : new FormControl('',([Validators.required, Validators.minLength(8),Validators.pattern(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,16}$/)])),
      "ConfirmPassword" : new FormControl('',([Validators.required]))
    },)
  }
   
// function passwordMatchValidator(g: FormGroup) {
// let pass = g.get('newPassword').value;
// let confPass = g.get('confirmPassword').value;
// if (pass != confPass) {
// g.get('confirmPassword').setErrors({ mismatch: true });
// } else {
// g.get('confirmPassword').setErrors(null)
// return null
// }
// }

   
  setData(){
    if (this.form.value.ConfirmPassword != this.form.value.NewPassword) {
      this.service.toastErr('password mismatched')
      return this.status == false;
      
    }
    else if((this.form.value.ConfirmPassword == this.form.value.NewPassword) && this.form.valid){
      this.status == true;
   let passwordData= {
   "adminId" : JSON.parse(localStorage.getItem('adminId')),
   "oldPassword" : this.form.value.OldPassword,
   "newPassword" : this.form.value.NewPassword,
   "confirmPassword" : this.form.value.ConfirmPassword
   }
   this.service.showSpinner()
   this.service.postApi('api/v1/admin/changePassword',passwordData,'').subscribe((res)=>{
        if(res['responseCode'] == 200){
          this.service.showSuccess(res['responseMessage'])
          this.router.navigate(['dashboard'])
          setTimeout(()=>{
            this.service.hideSpinner()
          },1000)
        }
        else{
          setTimeout(()=>{
            this.service.hideSpinner()
          },1000)
          this.service.toastErr(res['responseMessage'])
        }
   })
  }}

}
