import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';


@Component({
  selector: 'app-view-subadmin',
  templateUrl: './view-subadmin.component.html',
  styleUrls: ['./view-subadmin.component.css']
})
export class ViewSubadminComponent implements OnInit {

  subAdminId:any;
  permission:any;
  permissionarray:any= [];
  user:any=[];
  dummy:any;
  myPermission:any = [];

  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.search();
  }

  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.subAdminId=params.id
    })
  }
  search(){
    let postData={
      subAdminId:this.subAdminId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewSubAdmin`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.user=response['result'];
        this.permission = response['result']['permission'];
        var result = Object.entries(this.permission)
          for(var i = 0; i < result.length; i++) {
            for(var z = 0; z < result[i].length; z++) {
                if(result[i][z] == true){
                  this.myPermission.push(result[i]);
                }
            }
          }

        console.log('boolean',this.myPermission);
      }
      else{
        this.service.hideSpinner()
      }

    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong')
    })
  }




}
