import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-banner',
  templateUrl: './view-banner.component.html',
  styleUrls: ['./view-banner.component.css']
})
export class ViewBannerComponent implements OnInit {
  userId :any;
  user:any;
  bannerpic :any = " ";
  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
  }
 
   getId(){
     this.activatedRoute.params.subscribe((params)=> {
      this.userId = params
         console.log("Object=-=-=---=-->",this.userId)
            })
   }
   
   viewUser(){
     let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      bannerId : this.userId.id
                   }
     this.service.showSpinner()
     this.service.postApi('api/v1/admin/viewBanner',postData,'').subscribe((res)=>{
       if(res['responseCode']== 200){
         setTimeout(() => {
          this.service.hideSpinner()
         }, 1000);
         console.log('responsee',res)
         this.user= res['result']
         this.bannerpic = res['result']['image']
              }
            })
          }

}
