import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';
import { FormGroup, FormControl } from '@angular/forms';
declare var $ : any;
@Component({
  selector: 'app-advertise-management',
  templateUrl: './advertise-management.component.html',
  styleUrls: ['./advertise-management.component.css']
})
export class AdvertiseManagementComponent implements OnInit {
  pageNo:any=1;
  jobs:any=[];
  srNo:Number;
  searchkey:any = "";
  paginationData:any;
  deleteId:any;
  jobStatus:any;
  jobsId:any;
  limit:any=5;
  total:any;
  user: any;
  Array: any;
  messege: any;
  searchUserName:any = '';
  searchJobId:any ='';
  status: string;
  userId: any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  constructor(public router:Router , public service : DataService) { }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
    this.search(this.pageNo)

  }
  Reset(){
    this.searchkey = '';
     this.form.reset()
      this.searchByDate()
 }

  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.messege = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){

   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')

    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewAdv',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.user = res['result'];
        this.messege = res['responseMessage']
      }
      else{
        this.messege = res['responseMessage']

      }

    })
  }
}

  search(page){
    this.pageNo = page
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      pageNumber : this.pageNo,
      search : this.searchkey
    }
    this.service.postApi('api/v1/admin/viewAdv',postData,'').subscribe((res)=>{
    if(res['responseCode'] == 200){
    this.user = res['result']
  this.Array = res['result']['docs']
  this.paginationData = res['result']
  this.limit = res['result']['limit']
  this.total=res['result']['total']
  this.messege = res['responseMessage']
  this.srNo = (this.pageNo - 1) * this.paginationData.limit
}
else{
  this.messege = res['responseMessage']
}
},
(error)=>{
this.service.toastErr('something went wrong')
}

)

}

  view(id){
    this.router.navigate(['view-advertise/'+id]);
      }

  deleteJob(id){
    this.deleteId = id;
    $('#delete').modal('show');
    console.log('userId>>>>>>>>>>>',this.deleteId )
  }

  deleteUser(){
    let postData={
      adminId : JSON.parse(localStorage.getItem('adminId')),
      advertisementId: this.deleteId,
      status: 'DELETE'
    }
    this.service.postApi(`api/v1/admin/advAction`,postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.search(this.pageNo)
        this.service.showSuccess(response['responseMessage'])

      }
      else{
        this.service.toastErr('try again')
      }
    })
  }


  sliderRound(id,status)
  {
  this.status= status
  console.log(id,status)
  if(this.status=='ACTIVE'){

  this.status = "BLOCK"
  }
  $('#blocked').modal('show')
  console.log(id,status)
  console.log(id,this.status)
  this.userId = id;
  // var Status = status
  this.status = status;
  }
  blockCategory(id,status)
  {
  this.status= status
  if (this.status=='BLOCK'){
  this.status = "ACTIVE "
  }
  $('#activate').modal('show')

  // console.log(id,status)
  this.userId = id;
  this.status = status;
  }



  Block(){
  this.sliderRound(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
    this.status = 'BLOCK';
    }
    else{
    this.status = 'ACTIVE';
    }
  console.log(this.status)


  let apireq = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'advertisementId':this.userId,
  'status': this.status,
  }
  // console.log('apireq ==>>', apireq)
  this.service.postApi('api/v1/admin/advAction',apireq,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#blocked').modal('hide')
    this.service.toastErr(success['responseMessage'])
  this.search(this.pageNo)
  }
  },error => {
  console.log('Something went wrong');
  })

  }

  Activate(){
  this.blockCategory(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
    this.status = 'BLOCK';
    }
    else{
    this.status = 'ACTIVE';
    }
  console.log(this.status)
  let req = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'advertisementId':this.userId
  }
  this.service.postApi('api/v1/admin/advAction',req,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#activate').modal('hide')
    this.service.showSuccess(success['responseMessage'])
  this.search(this.pageNo)
  }
  },error => {
  console.log('Something went wrong');
  })
  }

  ViewApplicant(id){
   let ApplicantId = id
   console.log('id',ApplicantId)
  this.router.navigate(['view-applicant/'+id]);

  }
}
