import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare var $:any;

@Component({
  selector: 'app-all-report',
  templateUrl: './all-report.component.html',
  styleUrls: ['./all-report.component.css']
})
export class AllReportComponent implements OnInit {
  reportData: any;
  srNo :any = 0;
reportDocs : any;
limit:any;
pageNo:any;
total :any;
form:FormGroup;
toDate: string | number | Date;
newDate: string | number | Date;
oldDate: string | number | Date;
fromDate: string | number | Date;
  message: any;
  userId: any;
  constructor(private service : DataService,private router : Router) { }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
  this.listreport(this.pageNo)
  }

  Reset(){
    this.form.reset()
     this.searchByDate()
}
  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.message = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){

   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')

    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewAllReport',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.reportData = res['result']['docs'];
        this.limit =  res['result']['limit'];
        this.pageNo = res['result']['page'];
        this.total = res['result']['total'];
        this.message = res['responseMessage']
      }
      else{
        this.message = res['responseMessage']

      }

    })
  }
}

  listreport(page){
     this.pageNo = page
    let data = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      pageNumber : this.pageNo
    }
    this.service.postApi('api/v1/admin/viewAllReport',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.reportData = res['result']['docs'];
        console.log(' this.reportData ', this.reportData);

        this.limit =  res['result']['limit'];
        this.pageNo = res['result']['page'];
        this.total = res['result']['total'];
        this.message = res['responseMessage'];
      }
      else{
        this.message = res['responseMessage'];

      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })
  }

  viewReport(id){
    this.router.navigate(['view-all-report/'+id])

  }

  delete(id){
    this.userId = id
    $('#delete').modal('show');
    console.log('this user edit id====>',id)

  }

  deleteUser(){
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'queryId' : this.userId,
    }
    console.log("delete user id ===>",this.userId)
   this.service.postApi('api/v1/admin/deleteGeneralReport',postData,'').subscribe((res)=>{
        if(res['responseCode']==200){
          this.service.showSuccess(res['responseMessage'])
          $('#delete').modal('hide');
          this.listreport(this.pageNo)
       //location.reload()
        }
   }, (error)=>{
     this.service.toastErr('something went wrong')
   })
  }

}
