import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-industry',
  templateUrl: './edit-industry.component.html',
  styleUrls: ['./edit-industry.component.css']
})
export class EditIndustryComponent implements OnInit {
  industryName: any;
  id: any;
  data : any =[];
  itemData:any;
  filterData: any =[];
  obj:any;
  indutsryName:any;
  form:FormGroup;

  constructor(private service:DataService,private router :Router,private activatedroute : ActivatedRoute) { }

        ngOnInit() {
          this.activatedroute.params.subscribe((params)=>{
            this.id = params.id
                 });
                 this.form = new FormGroup({
                   "industryName" : new FormControl('',([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
                 })
          console.log('ID of the particular industry',this.id)
          this.listIndustry()
                  }

          inputKeyPress(e){
            var k
            if (e.charCode == 32 && !e.target.value) {
              event.preventDefault()
            }
            else {
              document.all ? k = e.keyCode : k = e.which;
              return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
            }
          }

  listIndustry(){
    let addData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      industryId : this.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/industryList',addData,'').subscribe((res)=>{
      console.log('data',res)
      if(res['responseCode'] == 200){
        setTimeout(() => {
          this.service.hideSpinner()    
        }, 1000);
      this.data = res['result']['docs'][0]['industryType']
      console.log('data',res,this.data)
      setTimeout(() => {
        this.form.patchValue({
          industryName : this.data
        })
      }, 1000);
      }
    },(err)=>{
      this.service.toastErr('Please try again')
    })

 }
       editIndustry(){
         let editData= {
          adminId : JSON.parse(localStorage.getItem('adminId')),
          industryId : this.id,
          industryType : this.form.value.industryName
         }
         this.service.showSpinner()
           this.service.postApi('api/v1/admin/editIndustry',editData,'').subscribe((res)=>{
               if(res['responseCode'] == 200){
                 this.service.hideSpinner()
                 this.industryName = res['result']['industryType']
                 this.router.navigate(['/Industry-management'])
               }

           },(error)=>{
             this.service.toastErr('something went wrong')
           })

       }

}
