import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-view-order',
  templateUrl: './view-order.component.html',
  styleUrls: ['./view-order.component.css']
})
export class ViewOrderComponent implements OnInit {
 id: any;
  data: any;
  user: any;
  createAt: any;
  deliveryDate: any;
  buyerInformation: any=[];
  allPosts: any;
  selectedValue :any;
  productIds: any = [];
  tab: any = 'view';
  form :FormGroup;
  constructor(public activatedRoute: ActivatedRoute , public service : DataService ,private router : Router) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params)=>{
      this.id = params.id
    });
    this.form  = new FormGroup({
      selectedValue : new FormControl('')
    })
    console.log(this.id)
    this.getId()
  }

   getId(){
     let listData = {
       adminId : JSON.parse(localStorage.getItem('adminId')),
       orderId : this.id
     }
     this.service.postApi('api/v1/admin/orderManagement',listData,'').subscribe((res)=>{
       if(res['responseCode'] == 200){
         this.user = res['result'];
         this.createAt = res['result'];
         this.deliveryDate = res['result'];
         let productId = [];
        //  productId =res['result']['productDescription'];
        //  productId.forEach(res=>{
        //    this.productIds.push(res.productId._id)
        //  })
        const buyerInfo = res['result']['userId']['personalDetail']
         buyerInfo.forEach((ele,i) => {
           if(ele.addressType === true){
               this.buyerInformation = ele
           }
         });
          this.allPosts =  res['allPost']
       }
     }
     )
   }

   tabchange(status){
    this.tab = status
   }

   changeProductStatus(){

    let data1= this.form.value.selectedValue.split(',');
      const item = {
        adminId : JSON.parse(localStorage.getItem('adminId')),
        orderId : this.id,
        productId :data1[1],
        "orderStatus": data1[0]
      }
      console.log('item of ProductStatus',item);
      this.service.postApi('api/v1/admin/editOrderManagement',item,'').subscribe((res)=>{
        console.log('res',res);
        if(res['responseCode'] == 200){
         this.router.navigate(['order-management'])
        }
        else{
          this.service.toastErr('Check your internet connection');
        }

      },(err)=>{
        this.service.toastErr('Something went wrong !');
      })
   }

}
