import { Component, OnInit } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { HeaderComponent } from '../header/header.component';
declare var $ :any;
@Component({
  selector: 'app-admin-profile',
  templateUrl: './admin-profile.component.html',
  styleUrls: ['./admin-profile.component.css']
})
export class AdminProfileComponent implements OnInit {
  form:FormGroup;
  type: any;
  file:any;
  fileData: any;
  adminName:any;
  user: ArrayBuffer;
  email:any;
  phone:any;
  adminId: any;
  constructor( private service : DataService,private router : Router,private header : HeaderComponent) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

        ngOnInit() {

        this.file = "assets/img/profile.png";
        // Form Control declare here
        this.form =  new FormGroup({
          "file": new FormControl(''),
          "phone" : new FormControl('',([Validators.required,Validators.pattern(/^[1-9][0-9]{9,13}$/)])),
          "adminName" : new FormControl('',([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)])),
          "lastName" :  new FormControl('',([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)])),
          "email" : new FormControl('',([Validators.required,Validators.pattern(/^([a-zA-Z0-9_\-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/i)]))

        })
         this.getData()
        }
       //For allowed only numbers
        inputKeyPress(e){
          var k
          if (e.charCode == 32 && !e.target.value) {
            event.preventDefault()
            }
            else{
              document.all ? k = e.keyCode : k = e.which;
              return ((k > 47 && k < 58) || k == 8);
            }
        }

        // Block special character and numbers
  inputkeyPress(e){
    var k
    document.all ? k = e.keyCode : k = e.which;
    return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
  }

        handleFileInput(event) {
          var self = this;
          if(event.target.files && event.target.files[0]){
            var type = event.target.files[0].type;
          this.type=type

          if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
          var reader = new FileReader()
          reader.onload = (e:any)=> {
            if(type === 'image/png' || type === 'image/jpg'){
              self.fileData = e.target['result'].substring(22);
            }
            else if (type === 'image/jpeg'){
              self.fileData = e.target['result'].substring(23);
            }
            this.file = e.target['result']
          }
          reader.readAsDataURL(event.target.files[0]);
          } else {
          this.service.toastErr("Select only jpg,jpeg and png file.");
          }
          }

        }

     async setData(){
          let data={
            "adminId" : JSON.parse(localStorage.getItem("adminId")),
            "phone" : this.form.value.phone,
            "firstName" : this.form.value.adminName,
            "lastName" : this.form.value.lastName,
            "profilePic" : this.file,
            "email" : this.form.value.email
          }
          this.service.showSpinner()
        await  this.service.postApi('api/v1/admin/editAdminProfile',data,'').subscribe((res)=>{

            if(res['responseCode'] == 200){
              this.service.showSuccess(res['responseMessage']);
             this.header.getProfile();
               setTimeout(()=>{
                this.service.hideSpinner();
                this.router.navigate(['dashboard']);
              },1000)
            }
          },(err)=>{
            this.service.toastErr('Something went wrong')
          });
          setTimeout(() => {
            this.header.getProfile();
          }, 2000);
        }

        getData(){
         this.service.showSpinner();
         let item = {
          adminId: JSON.parse(localStorage.getItem("adminId"))

         }
          this.service.postApi('api/v1/admin/adminProfile',item,'').subscribe((res:any)=>{
            if(res.responseCode == 200){
              setTimeout(() => {
                this.service.hideSpinner()
              }, 1000);
              this.user = res['result'];
              this.file = res['result']['profilePic'];

              this.form.patchValue({
                adminName : res.result.firstName,
                lastName : res.result.lastName,
                email : res.result.email,
                phone : res.result.phoneNumber
              })
            }
            else{
              this.service.hideSpinner()

            }
          },(error)=>{
            this.service.hideSpinner();
            this.service.toastErr('Somthing went wrong');
          }
          )
        }



    }
