import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-auction-transaction',
  templateUrl: './view-auction-transaction.component.html',
  styleUrls: ['./view-auction-transaction.component.css']
})
export class ViewAuctionTransactionComponent implements OnInit {

  id: any;
  data: any;
  user: any;
  createAt: any;
  deliveryDate: any;
  buyerInformation: any=[];
  allPosts: any;
  selectedValue :any;
  productIds: any = [];
  tab: any = 'view';
  form :FormGroup;
  paymentStatus: any;
  constructor(public activatedRoute: ActivatedRoute , public service : DataService ,private router : Router) { }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params)=>{
      this.id = params.id
    });
    this.form  = new FormGroup({
      selectedValue : new FormControl('')
    })
    console.log(this.id)
    this.getId()
  }

   getId(){
     let listData = {
       adminId : JSON.parse(localStorage.getItem('adminId')),
       orderId : this.id
     }
     this.service.postApi('api/v1/admin/auctionOrderManagement',listData,'').subscribe((res)=>{
       if(res['responseCode'] == 200){
         this.user = res['result'];
         this.paymentStatus = res['result']['auctionId']['status']
         console.log('user data',this.user);

         this.createAt = res['result'];
         this.deliveryDate = res['result'];
         let productId = [];
        //  productId =res['result']['productDescription'];
        //  productId.forEach(res=>{
        //    this.productIds.push(res.productId._id)
        //  })
        const buyerInfo = res['result']['userId']['personalDetail']
         buyerInfo.forEach((ele,i) => {
           if(ele.addressType === true){
               this.buyerInformation = ele
           }
         });
          this.allPosts =  res['allPost']
       }
     }
     )
   }

   tabchange(status){
    this.tab = status
   }

   changeProductStatus(){

    let data1= this.form.value.selectedValue.split(',');

      const item = {
        adminId : JSON.parse(localStorage.getItem('adminId')),
        orderId : this.id,
        // productId :data1[1],
        "orderStatus": data1[0]
      }
      this.service.postApi('api/v1/admin/editAuctionOrder',item,'').subscribe((res)=>{
        console.log('res',res);
        if(res['responseCode'] == 200){
          this.service.showSuccess(res['responseMessage'])
         this.router.navigate(['auction-order'])
        }
        else{
          this.service.toastErr('Check your internet connection');
        }

      },(err)=>{
        this.service.toastErr('Something went wrong !');
      })
   }


   payment(){
    let data= {
       orderId :this.id
    }
    this.service.postApi(`api/v1/admin/sellerTransferForAuction`, data,'').subscribe(response=>{
      if(response['responseCode']==200){
        // this.search()
        this.service.showSuccess('The payment successfully')
        // console.log("to check userDetails")
      }
      else{
        this.service.toastErr('Please check product status')
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })

 }


refund(){
    let data= {
       orderId : this.id
    }
    this.service.postApi(`api/v1/admin/refundForAuction`, data,'').subscribe(response=>{
      if(response['responseCode']==200){
        // this.search()
         this.router.navigate(['/transactionManag-auction'])
        this.service.showSuccess('The refund successfully')
      }
      else{
        this.service.toastErr('Please check product status')
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })




}


}
