import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-buying-transaction',
  templateUrl: './view-buying-transaction.component.html',
  styleUrls: ['./view-buying-transaction.component.css']
})
export class ViewBuyingTransactionComponent implements OnInit {
  userId:any;
  user:any=[];
  createAt: any;
  deliveryDate: any;
  orderStatus: any;
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.search();
  }
  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.userId=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.userId,"KKKKKKKKKK",params)
    })
  }
  search(){
    let postData={
      transactionId:this.userId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi(`api/v1/admin/transactionManag`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.user=response['paymentResult'];
        this.orderStatus = response['paymentResult']['orderStatus']
        this.createAt = response['paymentResult']['createdAt'];
        this.deliveryDate = response['paymentResult']['deliveryDate'];
        console.log("to check userDetails", this.user)
      }
    })
  }
  payment(){
        let data= {
          orderId : this.userId
        }
        this.service.postApi(`api/v1/admin/sellerPaymentTransfer`, data,'').subscribe(response=>{
          if(response['responseCode']==200){
            this.search()
            this.service.showSuccess('The payment successfully')
            // console.log("to check userDetails")
          }
          else{
            this.service.toastErr('Please check product status')
          }
        },(err)=>{
          this.service.toastErr('Something went wrong')
        })
        this.service.toastErr('Please check order status')

     }


  refund(){
        let data= {
          orderId : this.userId
        }
        this.service.postApi(`api/v1/admin/refundForProduct`, data,'').subscribe(response=>{
          if(response['responseCode']==200){
            this.search()
            this.service.showSuccess('The refund successfully')
            console.log("to check userDetails")
          }
          else{
            this.service.toastErr('Please check product status')
          }
        },(err)=>{
          this.service.toastErr('Something went wrong')
        })




  }

}
