import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-sticker',
  templateUrl: './add-sticker.component.html',
  styleUrls: ['./add-sticker.component.css']
})
export class AddStickerComponent implements OnInit {
  fileToUpload: File = null;
  obj:any ={};
  form:FormGroup;
  base64Image: any;
  upload: any;
  image : any;
  type: any;
  fileData: any;
  imageUrl:any='';

  constructor(private service : DataService,private fb :FormBuilder,private router :Router) { }

  ngOnInit() {
    this.form = this.fb.group({
      "file": new FormControl('',Validators.required),
      "stickerName" : new FormControl('',Validators.compose([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
     });

     this.imageUrl = "assets/img/profile.png"
     
  }
   
  // Block special character and numbers from title
  inputKeyPress(e){
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
      }
  }
  
  handleFileInput(event) {
    var self = this;
    if(event.target.files && event.target.files[0]){
      var type = event.target.files[0].type;
    this.type=type

    if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
    var reader = new FileReader()
    reader.onload = (e:any)=> {
      if(type === 'image/png' || type === 'image/jpg'){
        self.fileData = e.target['result'].substring(22);
      } 
      else if (type === 'image/jpeg'){
        self.fileData = e.target['result'].substring(23);
      } 
      this.imageUrl = e.target['result']
    }
    reader.readAsDataURL(event.target.files[0]);
    
    
    } else {
    this.service.toastErr("Select only jpg,jpeg and png file.");
    }
    }  

  }

  uploadFile() {
   
    let data ={
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "stickerName": this.form.value.stickerName,
      "image":  'data:'+this.type +'/;base64,'+this.fileData,

    }
   this.service.showSpinner()
   this.service.postApi('api/v1/admin/addSticker',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
         this.service.hideSpinner()
        this.imageUrl = res['stickerData']['image']
        this.service.showSuccess(res['responseMessage'])
        this.router.navigate(['sticker-management'])
      }
    else  {
      this.service.toastErr('retry again!')
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })
   
    }
}
