import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';
@Component({
  selector: 'app-view-events',
  templateUrl: './view-events.component.html',
  styleUrls: ['./view-events.component.css']
})
export class ViewEventsComponent implements OnInit {

  eventId:any;
  event:any=[];
  shares: any = [];
  likes: any = [];
  comments: any = [];
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.viewEvent();
  }

  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.eventId=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.eventId,"KKKKKKKKKK",params)
    })
  }
  viewEvent(){
    let postData={
      eventId:this.eventId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewEvent`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        setTimeout(() => {
          this.service.hideSpinner()   
        }, 1000);
        this.event=response['result'];
        this.likes = response['result']['likes'];
        this.comments = response['result']['comments']
        this.shares = response['result']['sharedBy']


        console.log("to check userDetails", this.event)
      }
      else{
        this.service.hideSpinner()
      }
    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong');
    })
  }
}
