import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-help-edit',
  templateUrl: './help-edit.component.html',
  styleUrls: ['./help-edit.component.css']
})
export class HelpEditComponent implements OnInit {

  helpForm: FormGroup;
  type: any;
  titleData:any;
  privacyView: any;
  editorValue: string;
  config: {
    uiColor: '#F0F3F4',
    height: '100%'
  }
  editData: any;

  constructor(public router: Router, public service: DataService) {

   
  }

  ngOnInit() {
    this.helpForm = new FormGroup({
      editorValue : new FormControl('',([Validators.required,Validators.maxLength(512)])),
      Title : new FormControl('',([Validators.required, Validators.minLength(3),Validators.pattern(/^[^\s][A-Za-z&\s]*$/)]))
      
    })
    this.staticApi();

  }

  staticApi() {
    let viewData = {
      type: 'HELP'
    }
      this.service.showSpinner()
     this.service.postApi('api/v1/staticPage/staticApi', viewData, '').subscribe(response => {
       setTimeout(() => {
        this.service.hideSpinner()
       }, 1000);
      if (response['responseCode'] == 200) {
        this.privacyView = response["success"]
        this.editData = response["success"]['description'];
        this.titleData = response['success']['title'];
        setTimeout(() => {
          this.helpForm.patchValue({
            editorValue :this.editData,
            Title : this.titleData
          })
        }, 1000);
      
      }
      else {
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      this.service.toastErr('Something Went Wrong')
    })
  }
  editNews() {
    let addData = {
      type: 'HELP',
      "description": this.helpForm.value.editorValue,
      "title" : this.helpForm.value.Title
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/staticPage/staticPageUpdate', addData, '').subscribe(response => {
      console.log("in success>>>>>", response)
      if (response['responseCode'] == 200) {
        this.service.hideSpinner()
        this.service.showSuccess(response['responseMessage'])
        this.router.navigate(['static-content-management'])
      }
      else {
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      console.log(">>>>>>>>>>>", error)
      this.service.toastErr('Something Went Wrong')
    })
  }

}
