import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-auction-product',
  templateUrl: './view-auction-product.component.html',
  styleUrls: ['./view-auction-product.component.css']
})
export class ViewAuctionProductComponent implements OnInit {
  id:any;
  productDetails: any;
  constructor(private service : DataService,private router : Router,private activatedroute : ActivatedRoute) { }

  ngOnInit() {
    this.activatedroute.params.subscribe((params)=>{
      this.id = params.id
      console.log('this is id of view Product',this.id)
      });
      this.viewProduct()
  }
  viewProduct(){
    let data = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      productId : this.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewAuctionProduct',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        setTimeout(() => {
          this.service.hideSpinner()      
        }, 1000);
        this.productDetails = res['result']['docs'][0]
      }
     
    },err=>{
      this.service.toastErr('Please try again');
    })
  }
}
