import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-news',
  templateUrl: './view-news.component.html',
  styleUrls: ['./view-news.component.css']
})
export class ViewNewsComponent implements OnInit {

  newsId:any;
  news:any=[];
  newsPic: any = [];
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.viewNews();
  }

  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.newsId=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.newsId,"KKKKKKKKKK",params)
    })
  }
  viewNews(){
    let postData={
      newsId:this.newsId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewNews`, postData,'').subscribe(response=>{
      console.log("ytyufregjhskdjgfiasg",response)
      if(response['responseCode']==200){
        setTimeout(() => {
          this.service.hideSpinner() 
        }, 1000);
        this.news=response['result'];
        this.newsPic = response['result']['newsPic']
        
        console.log("to check userDetails", this.newsPic)
      }
      else{
        this.service.hideSpinner()
      }
    },(err)=>{
      this.service.hideSpinner()
      this.service.toastErr('Something went wrong')
    })
  }

}
