            import { Component, OnInit } from '@angular/core';
            import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
            import { DataService } from '../services/data.service';
            import { Router } from '@angular/router';
            declare var $:any;
            @Component({
              selector: 'app-forgot-password',
              templateUrl: 'forgot-password.component.html',
              styleUrls: ['forgot-password.component.css']
            })
            export class ForgotPasswordComponent implements OnInit {
              forgotForm:FormGroup;
              constructor(private formBuilder:FormBuilder, public dataService: DataService,private router : Router) { }

              ngOnInit() {
              
              
            this.forgotForm=this.formBuilder.group({
              email: new FormControl('', Validators.compose([Validators.required,Validators.email,Validators.pattern(/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})+$/)]))
            })
            }
            get f(){ return this.forgotForm.controls;}
            onForgot()
                  {
                    
                    console.log("forgot form data", this.forgotForm.value)
                  this.dataService.showSpinner()
                    this.dataService.postApi("api/v1/admin/forgotPassword",{
                      'email': this.forgotForm.value.email}, "").subscribe(
                      (res: any)=>{
                        console.log("response", res);
                        
                        if(res.responseCode == 200){
                          setTimeout(() => {
                            this.dataService.hideSpinner()
                          }, 1000);
                          this.dataService.showSuccess(res.responseMessege);
                          
                            // $('#modalPush').modal('show')
                          
                          // this.router.navigate(['reset-password'])
                          console.log("response", res);

                        }
                        else if(res.responseCode== 404)
                        {
                          this.dataService.toastErr(res.responseMessege);
                          this.dataService.hideSpinner()
                        }
                        else{
                          this.dataService.toastErr(res.responseMessege);
                          this.dataService.hideSpinner()

                        }

                      },
                      (err: any)=>{
                        console.log("error", err);
                        this.dataService.hideSpinner()
                      }
                    )

                    }

                    // otpverify(){
                    //   $('#modalPush').modal('hide')

                    // }
                  }
