import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../services/data.service';
import { ActivatedRoute } from '@angular/router'
@Component({
  selector: 'app-edit-news',
  templateUrl: './edit-news.component.html',
  styleUrls: ['./edit-news.component.css']
})


export class EditNewsComponent implements OnInit {

  news: FormGroup;
  type: any;
  base64Image: any;
  cloudinaryUrl: any = [];
  id: any;
  newsView: any = [];
  type1: any;
  type2: any;
  // type: any = [];
  urls: any = [];
  videoUrls: any = []
  newsData: any = []
  url: any = [];
  news1data = new Array<string>();
  news2data = new Array<string>();
  titleData: any;
  description: any;
  picId: any = [];
  ImageData: any =[];

  constructor(public router: Router, public service: DataService, public activatedRoute: ActivatedRoute) { }
  ngOnInit() {
    this.news = new FormGroup({
      'newsPic': new FormControl(''),
      "title": new FormControl('', ([Validators.required, Validators.minLength(8), Validators.pattern(/^[^\s][A-Za-z\s]*$/)])),
      "description": new FormControl('', ([Validators.required, Validators.minLength(8)])),
    })
    this.getId();
    this.viewNews();
  }

  getId() {
    this.activatedRoute.params.subscribe(params => {
      this.id = params.id
      console.log(">>>>>>>>>>>>>>>>>>>>", this.id, "KKKKKKKKKK", params)
    })
  }

  preventSpace(event) {
    if (event.charCode == 32 && !event.target.value) {
      event.preventDefault()
    }
  }

  blockspecialcharacter(e) {
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
    }
    else {
      document.all ? k = e.keyCode : k = e.which;
      return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
    }
  }

  viewNews() {
    let viewData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      newsId: this.id

    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewNews', viewData, '').subscribe((response: any) => {
      if (response['responseCode'] == 200) {
        setTimeout(() => {
          this.service.hideSpinner()
        }, 1000);
        this.ImageData = response["result"]['newsPic'];
        console.log('Image pic',this.newsData)
        for (var i = 0; i < this.ImageData.length; i++) {
          this.picId.push(this.ImageData[i]._id)
          this.newsData.push(this.ImageData[i].image)
         
        }
        console.log('PicId', this.picId,this.newsData)
        this.titleData = response['result']['title'];
        this.description = response['result']['title']
        this.news.patchValue({
          title: this.titleData,
          description: this.description
        })
      }
      else {
        this.service.hideSpinner()
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      this.service.hideSpinner()
      console.log(">>>>>>>>>>>", error)
      this.service.toastErr('Something Went Wrong')
    })
  }

  handleFileInput(event) {
    this.urls = [];
    this.newsData = [];
    this.videoUrls = [];
    let files = event.target.files;
    if (files.length > 5) {
      setTimeout(() => {
        this.service.toastErr("Maximum 5 files allow only");
      }, 1000);
      return;
    }
    console.log(files.length)
    if (files) {
      for (let file of files) {
        console.log("file data===>", file.size, file)
        if (file.size > 5242880) {
          setTimeout(() => {
            this.service.toastErr("File size should not be greater than 10MB.");
          }, 1000);
          return false;
        }

        else if (file.size <= 5242880) {
          if (file.type == "image/jpeg" || file.type == "image/jpg" || file.type == "image/png") {
            this.type1 = file.type
            var reader = new FileReader();
            reader.onload = (e: any) => {
              let data = e.target
              this.urls.push(data.result);
              console.log(this.urls, "This is the urls")

            };

            reader.readAsDataURL(file);
          }

          else if (file.type == "video/mp4" || file.type == 'video/mov' || file.type == 'video/flv') {
            this.type2 = file.type
            reader = new FileReader();
            reader.onload = (e: any) => {
              this.videoUrls.push(e.target.result);
              console.log(this.videoUrls, "This is the Video urls")

            }
            reader.readAsDataURL(file);
          }
          else {
            this.service.toastErr("Select only jpg,jpeg,png,mp4,mov or flv file.");
          }

        }
      }
      setTimeout(() => {
        this.service.showSpinner()
        this.urls.map(x => {
          this.newsData.push(x)
          console.log(this.newsData, "This is the urls")

        })
        this.videoUrls.map(x => {
          this.newsData.push(x)
        })
        setTimeout(() => {
          console.log(this.newsData, 'this is the after set time out')
        }, 1000);
        this.service.hideSpinner()
      }, 1000);

    }
  }
  // to uplaod file


  editNews() {

    let addData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      newsId: this.id,
      "imageId": this.picId,
      "image": this.newsData,
      "title": this.news.value.title,
      "description": this.news.value.description,
    }

    // console.log("hello", this.news.value,"url of image", this.cloudinaryUrl, "in req data", addData)

    this.service.postApi('api/v1/admin/updatedNews', addData, '').subscribe(response => {
      console.log("in success>>>>>", response)
      if (response['responseCode'] == 200) {
        this.service.showSuccess(response['responseMessage'])
        this.router.navigate(['news-management'])
      }
      else {
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      console.log(">>>>>>>>>>>", error)
      this.service.toastErr('Something Went Wrong')
    })
  }


}

