import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-gif',
  templateUrl: './view-gif.component.html',
  styleUrls: ['./view-gif.component.css']
})
export class ViewGifComponent implements OnInit {
  userId:any;
  user:any;
  Gifpic:any;

  constructor(private activatedRoute : ActivatedRoute,private service :DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
  }

  getId(){
    this.activatedRoute.params.subscribe((params)=> {
     this.userId = params
    })
  }
  
  viewUser(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     gifId : this.userId.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewGif',postData,'').subscribe((res)=>{
      if(res['responseCode']== 200){
        setTimeout(() => {
          this.service.hideSpinner()      
        }, 1000);
        this.user= res['gifData']
        this.Gifpic = res['gifData']['gif']

      }
      else{
        this.service.hideSpinner()
      }
    },(err)=>{
      this.service.hideSpinner()
    })
  }
}
