import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare var $: any;
@Component({
  selector: 'app-auction-category-management',
  templateUrl: './auction-category-management.component.html',
  styleUrls: ['./auction-category-management.component.css']
})
export class AuctionCategoryManagementComponent implements OnInit {
  pageNo: any = 1;
  form:FormGroup;

  searchkey: any = '';
  user: any = [];
  paginationData: any;
  srNo: number;
  deleteId: any;
  status: any;
  userId: any;
  limit: any;
  total: any;
  Array: any;
  messege: any;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  

  constructor(public router: Router, public service: DataService) { 
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
    this.search(this.pageNo)

  }

  // Router navigate to view Screen
  view(id) {
    console.log("subAdmin id>>>>>>>>>>", id)
    this.router.navigate(['view-auction-category/' + id])
  }
  // Router navigate to edit Screen
  edit(id) {
    console.log("edit Id>>>>", id)
    this.router.navigate(['edit-auction-category/' + id])
  }
  // method for search and list category

  search(page) {
    this.pageNo = page;
    let postData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      pageNumber: this.pageNo,
      search: this.searchkey
    }
    this.service.postApi(`api/v1/admin/viewAuctionCategory`, postData, '').subscribe(response => {
      if (response['responseCode'] == 200) {
        this.user = response['result']
        this.messege = response['responseMessage']
        this.Array = response['result']['docs']
        this.paginationData = response['result']
        this.total = response['result']['total']
        this.limit = response['result']['limit']
        this.srNo = (this.pageNo - 1) * this.paginationData.limit
        this.service.setCategory(this.Array)
      }
      else {
        this.messege = response['responseMessage']
      }
    }, error => {
      this.service.toastErr('Something Went Wrong')

    })
  }
 
  Reset(){
    this.searchkey= ''
    this.form.reset()
     this.searchByDate()
}
 // Search by Date
 searchByDate(){
  if(this.form.value.fromDate > this.form.value.toDate){
    this.service.toastErr('From date should be less  than todate')
    // this.form.value.fromDate = '';
    this.messege = "please enter right date"
  }
  else if(this.form.value.fromDate <= this.form.value.toDate){
  
 this.oldDate = new Date(this.form.value.fromDate).toISOString()
    this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
  
  let dummyDateData = {
    "adminId":JSON.parse(localStorage.getItem('adminId')) ,
   "fromDate": this.oldDate,
  "toDate":  this.newDate
  }
  this.service.postApi('api/v1/admin/viewAuctionCategory',dummyDateData,'').subscribe((res)=>{
    if(res['responseCode'] == 200){
      this.user = res['result'];
      this.messege = res['responseMessage']
    }
    else{
      this.messege = res['responseMessage']

    }
   
  })
}
}

//  For open activate modal
  sliderRound(id, status) {
    this.status = status
    if (this.status == 'ACTIVE') {

      this.status == "BLOCK"
    }
    $('#blocked').modal('show')
    console.log(id, status)
    console.log(id, this.status)
    this.userId = id;
    this.status = status;
  }
  // For open block modal 
  blockCategory(id, status) {
    this.status = status
    if (this.status == 'BLOCK') {
      this.status = "ACTIVE "
    }
    $('#activate').modal('show')
    this.userId = id;
    this.status = status;
  }

    // Api integration for block catgory
    Block() {
    this.sliderRound(this.userId, this.status)
    console.log('this is sthe id and status', this.userId, this.status)
    if (this.status == 'ACTIVE') {
      this.status = 'BLOCK';
    }
    else {
      this.status = 'ACTIVE';
    }
    console.log(this.status, this.userId)
    let apireq = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      'auctionCategoryId': this.userId,
      'status': this.status,
    }
    console.log('apireq ==>>', apireq)
    this.service.postApi('api/v1/admin/auctionCategoryManagment', apireq, '').subscribe((success: any) => {
      console.log(success)
      if (success.responseCode === 200) {
        $('#blocked').modal('hide')
        this.service.toastErr(success['responseMessage'])
        this.search(this.pageNo)
      }
    }, error => {
      console.log('Something went wrong');
    })

  }
// Api integration for activate catgory
  Activate() {
    this.blockCategory(this.userId, this.status)
    console.log('this is sthe id and status', this.userId, this.status)
    if (this.status == 'ACTIVE') {
      this.status = 'BLOCK';
    }
    else {
      this.status = 'ACTIVE';
    }
    let req = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      'auctionCategoryId': this.userId
    }
    this.service.postApi('api/v1/admin/auctionCategoryManagment', req, '').subscribe((success: any) => {
      console.log(success)
      if (success.responseCode === 200) {
        $('#activate').modal('hide')
        this.service.showSuccess(success['responseMessage'])
        this.search(this.pageNo)
      }
    }, error => {
      console.log('Something went wrong');
    })
  }
 
   // for open delete modal
  deleteJob(id) {
    this.deleteId = id;
    $('#delete').modal('show');
    console.log('userId>>>>>>>>>>>', this.deleteId)
  }
 // delete api integration
  deleteUser() {
    let postData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      auctionCategoryId: this.deleteId,
      status: 'DELETE'
    }
    this.service.postApi(`api/v1/admin/auctionCategoryManagment`, postData, '').subscribe(response => {
      if (response['responseCode'] == 200) {
        this.search(this.pageNo)
        this.service.showSuccess(response['responseMessage'])
      }
      else {
        this.service.toastErr(response['responseMessage'])
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })
  }


}
