import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service'
import { FormGroup, FormControl } from '@angular/forms';
import { query } from '@angular/animations';
// import { $ } from 'protractor';
declare var $: any;
@Component({
  selector: 'app-jobmanagement',
  templateUrl: './jobmanagement.component.html',
  styleUrls: ['./jobmanagement.component.css']
})
export class JobmanagementComponent implements OnInit {

  pageNo: any = 1;
  jobs: any = [];
  srNo: Number;
  searchkey: any = "";
  paginationData: any;
  deleteId: any;
  jobStatus: any;
  jobsId: any;
  limit: any;
  total: any;
  user: any = [];
  Array: any;
  messege: any;
  searchUserName: any = '';
  searchJobId: any = '';
  status: string;
  userId: any;
  form: FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  applicantId: any = [];


  constructor(public router: Router, public service: DataService) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

  ngOnInit() {
    this.form = new FormGroup({
      "fromDate": new FormControl(''),
      "toDate": new FormControl('')
    });
    this.search(this.pageNo);
    this.service.BlockFuture();
  }
  search(page) {
    this.pageNo = page
    let postData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      page: this.pageNo,
      search: this.searchkey
    }
    this.service.postApi('api/v1/admin/viewjob', postData, '').subscribe((res) => {
      if (res['responseCode'] == 200) {
        this.user = res['results']
        this.paginationData = res['results'];
          // this.pageNo = res['totalpage']
        this.total = res['total']
        this.messege = res['responseMessage']
        // this.srNo = (this.pageNo - 1) * this.paginationData.limit
      }
      else {
        this.messege = res['responseMessage']
      }
    },
      (error) => {
        this.service.toastErr('something went wrong')
        this.messege = error['responseMessage']

      }

    )

  }
 
    

  view(id,applicantId) {
    console.log(id,applicantId)
     this.applicantId = applicantId;
     let data= {
       "jobId" : id,
       "applicantId" : this.applicantId
     }
    

    this.router.navigate(['manage-records'],{  queryParams: {value: JSON.stringify(data)}});
  }

  

  // edit(id){
  //   console.log(id)
  //   this.router.navigate([])
  // }
  deleteJob(id, status) {
    this.deleteId = id;
    $('#delete').modal('show');
    console.log('userId>>>>>>>>>>>', this.deleteId)
  }

  deleteUser() {
    let postData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      jobId: this.deleteId,
      jobStatus: 'DELETE'
    }
    this.service.postApi(`api/v1/admin/jobManagement`, postData, '').subscribe(response => {
      if (response['responseCode'] == 200) {
        this.search(this.pageNo)
        this.service.showSuccess(response['responseMessage'])

      }
      else {
        this.service.toastErr('try again')
      }
    })
  }


  sliderRound(id, jobStatus) {
    this.status = jobStatus
    console.log(id, jobStatus)
    if (this.status == 'ACTIVE') {
      this.status = "BLOCK"
    }
    $('#blocked').modal('show')
    console.log(id, jobStatus)
    console.log(id, this.status)
    this.userId = id;
    this.status = jobStatus;
  }
  blockCategory(id, jobStatus) {
    this.status = jobStatus
    if (this.status == 'BLOCK') {
      this.status = "ACTIVE "
    }
    $('#activate').modal('show')

    // console.log(id,status)
    this.userId = id;
    this.status = jobStatus;
  }


  Block() {
    this.sliderRound(this.userId, this.status)
    console.log('this is sthe id and status', this.userId, this.status)
    if (this.status == 'ACTIVE') {
      this.status = 'BLOCK';
    }
    else {
      this.status = 'ACTIVE';
    }
    console.log(this.status)


    let apireq = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      'jobId': this.userId,
      'jobStatus': this.status,
    }
    // console.log('apireq ==>>', apireq)
    this.service.postApi('api/v1/admin/jobManagement', apireq, '').subscribe((success: any) => {
      console.log(success)
      if (success.responseCode === 200) {
        $('#blocked').modal('hide')
        this.service.toastErr(success['responseMessage'])
        this.search(this.pageNo)
      }
    }, error => {
      console.log('Something went wrong');
    })

  }

  Activate() {
    this.blockCategory(this.userId, this.status)
    console.log('this is sthe id and status', this.userId, this.status)
    if (this.status == 'ACTIVE') {
      this.status = 'BLOCK';
    }
    else {
      this.status = 'ACTIVE';
    }
    console.log(this.status)
    let req = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      'jobId': this.userId,
      'jobStatus': this.status
    }
    this.service.postApi('api/v1/admin/jobManagement', req, '').subscribe((success: any) => {
      console.log(success)
      if (success.responseCode === 200) {
        $('#activate').modal('hide')
        this.service.showSuccess(success['responseMessage'])
        this.search(this.pageNo)
      }
    }, error => {
      console.log('Something went wrong');
    })
  }

  ViewApplicant(id,_id) {
    let ApplicantId = id;
    let item = {
      jobId : id,
      applicantId : _id
    }
    console.log('id', ApplicantId)
    this.router.navigate(['manage-records'],{  queryParams: {value: JSON.stringify(item)},skipLocationChange: true});
    window.history.pushState('','',item.jobId)
  }

  Reset(){
    this.searchkey = '';
     this.form.reset()
      this.searchByDate()
 }

          // Search by Date
          searchByDate(){
            if(this.form.value.fromDate > this.form.value.toDate){
              this.service.toastErr('From date should be less  than todate')
              // this.form.value.fromDate = '';
              this.messege = "please enter right date"
            }
            else if(this.form.value.fromDate <= this.form.value.toDate){
            
           this.oldDate = new Date(this.form.value.fromDate).toISOString()
              this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
            
            let dummyDateData = {
              "adminId":JSON.parse(localStorage.getItem('adminId')) ,
             "fromDate": this.oldDate,
            "toDate":  this.newDate
            }
            this.service.postApi('api/v1/admin/viewjob',dummyDateData,'').subscribe((res)=>{
              if(res['responseCode'] == 200){
                this.user = res['results'];
                this.total=res['total'];
                this.limit = res['results']['limit'];
                // this.pageNo = res['totalpage']
                this.messege = res['responseMessage']
              }
              else{
                this.messege = res['responseMessage']
         
              }
             
            })
          }
        }
}
