import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';
@Component({
  selector: 'app-view-terms',
  templateUrl: './view-terms.component.html',
  styleUrls: ['./view-terms.component.css']
})
export class ViewTermsComponent implements OnInit {

  termsId:any;
  terms:any=[];
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.search();
  }
// Get terms data form Api
  search(){
    let postData={
      type:'TERMS',
    } 
     this.service.showSpinner()
    this.service.postApi(`api/v1/staticPage/staticApi`, postData,'').subscribe(response=>{
      console.log("ytyufregjhskdjgfiasg",response)
      if(response['responseCode']==200){
        this.terms=response["success"];
        this.service.hideSpinner()
        // console.log("to check userDetails", this.terms)
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })
  }

}
