import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../services/data.service';
import { Router} from '@angular/router'
@Component({
  selector: 'app-login',
  templateUrl: 'login.component.html',
  styleUrls: ['login.component.css']
})
export class LoginComponent implements OnInit {

  loginForm: FormGroup;
  user: any;
  token: any;
  submitted=false;
  response : any ={}
  imageUrl: string;
  headerData: void;

  constructor(private formBuilder:FormBuilder, public dataService: DataService ,   public router:Router ) { 
  }

  ngOnInit() {
       this.imageUrl = "assets/img/Logo-1.PNG"
    var remData=JSON.parse(localStorage.getItem('rememberMe'))?JSON.parse(localStorage.getItem('rememberMe')):'';
    let pass='';
    let email = '';
   

    this.loginForm = this.formBuilder.group({
      email: new FormControl('',  Validators.compose([Validators.required,Validators.email,Validators.pattern(/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})+$/)])),
      password: new FormControl('',Validators.compose([Validators.required,Validators.maxLength(16), Validators.minLength(8)])),
      rememberMe: new FormControl(false)
    })

    if(remData!=''){
      // pass=window.atob(remData.password)
      // email = window.atob(remData.email)
      this.loginForm.patchValue({
        email: window.atob(remData.email),
        password: window.atob(remData.password)
      })
      console.log("rem-->>",remData,this.loginForm.value)
      }
    
  }
  get f(){ return this.loginForm.controls;}

  
  onSubmit()
  {
    this.dataService.showSpinner()
    this.dataService.postApi("api/v1/admin/login",{
      'email': this.loginForm.value.email,
      'password': this.loginForm.value.password,
       'rememberMe': this.loginForm.value.rememberMe
    }, "").subscribe(
 
      (res: any)=>{
        console.log("response", res);
          // console.log('Form Data',this.loginForm.value)
        if(res.responseCode == 200){
          setTimeout(()=>{
            this.dataService.hideSpinner()
          },1000)
          localStorage.setItem('adminId', JSON.stringify(res.result.adminId));
          localStorage.setItem('token', JSON.stringify(res.result.token));
          let userType = window.btoa(res.result.userType)
          localStorage.setItem('userType',JSON.stringify(userType))
            //  this.router.navigate(['/dashboard'])
           
          this.router.navigateByUrl('/dashboard')
          this.dataService.showSuccess(res['responseMessege'])
          if(this.loginForm.value.rememberMe==true){
            let remData={
              "email":window.btoa(this.loginForm.value.email),
              "password":window.btoa(this.loginForm.value.password)
            }
           
            console.log('remData',remData)
            localStorage.setItem('rememberMe',JSON.stringify(remData))
           
        }
        else{
          localStorage.removeItem("rememberMe");
         
        }
       
        }
        else{
          setTimeout(()=>{
            this.dataService.hideSpinner()
          },1000)
         this.dataService.toastErr(res['responseMessege'])
        
        }
      },
      (err: any)=>{
        setTimeout(()=>{
          this.dataService.hideSpinner()
        },1000)
       this.dataService.toastErr('please try again')
      
      }
    )
    this.submitted=true;
    if(this.loginForm.invalid)
    {
      return false;
    }
     console.log("success")
     }

}
