import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-about',
  templateUrl: './view-about.component.html',
  styleUrls: ['./view-about.component.css']
})
export class ViewAboutComponent implements OnInit {

  aboutId:any;
  about:any=[];
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.search();
  }
  search(){
    let postData={
      type:'ABOUT_US',
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/staticPage/staticApi`, postData,'').subscribe(response=>{
      console.log("ytyufregjhskdjgfiasg",response)
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.about=response["success"];
        console.log("to check userDetails", this.about)
      }
    },(err)=>{
      this.service.hideSpinner()
      this.service.toastErr('Something went wrong')
    })
  }

}
