import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
declare var $:any;
@Component({
  selector: 'app-payment-management',
  templateUrl: './payment-management.component.html',
  styleUrls: ['./payment-management.component.css']
})
export class PaymentManagementComponent implements OnInit {

  form:FormGroup;
  result: ArrayBuffer;
  paymentId: any;
    constructor(private service:DataService,private router :Router) {
      $('.btn-toggle,.close_panel').click(() => {
        $('body').toggleClass('toggle-wrapper');
      });
     }

  ngOnInit() {
   this.form = new FormGroup({
    videoGame : new FormControl('',([Validators.required,Validators.pattern(/^[0-9]{0,15}(\.[0-9]+)?$/)])),
    adevrtisement : new FormControl('',([Validators.required,Validators.pattern(/^[0-9]{0,15}(\.[0-9]+)?$/)])),
    job : new FormControl('',([Validators.required,Validators.pattern(/^[0-9]{0,15}(\.[0-9]+)?$/)]))
   });
   this.getPayment()
  }

  // Block special character and numbers from title
  inputKeyPress(e){
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 47 && k < 58) || k == 8 || k== 46 );
      }
  }

  setpayment(){
    let data = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      paymentForVideo : Number(this.form.value.videoGame),
      paymentForAdvertisement : Number(this.form.value.adevrtisement),
      paymentForJob : Number(this.form.value.job)
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/setPayment',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.paymentId = res['result']['_id'];
       localStorage.setItem('pay_Id',this.paymentId)
        setTimeout(()=>{
          this.service.hideSpinner()
        },1000)
        this.router.navigate(['/dashboard'])
        this.service.showSuccess(res['responseMessage'])
      }
    })
  }

  getPayment(){

    const item = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      "paymentId" : localStorage.getItem('pay_Id')
    }
    this.service.postApi('api/v1/admin/getSetPayment',item,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
      this.result = res['data'];
      this.form.patchValue({
        videoGame : this.result['paymentForVideo'],
        adevrtisement : this.result['paymentForAdvertisement'],
        job : this.result['paymentForJob']
      })
      }
    })
  }

}
