import { Component, OnInit } from '@angular/core';
import { Router} from '@angular/router';
import { DataService } from '../services/data.service'
import { FormGroup, FormControl } from '@angular/forms';
declare var $ : any;
@Component({
  selector: 'app-eventmanagement',
  templateUrl: './eventmanagement.component.html',
  styleUrls: ['./eventmanagement.component.css']
})
export class EventmanagementComponent implements OnInit {
  pageNo:any=1;
  events:any=[];
  srNo:Number;
  searchkey1:any;
  paginationData:any;
  deleteId:any;
  status:any;
  eventId:any;
  limit:any;
  total:any;
  userId: any;
  message: any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;


  constructor(public router:Router , public service : DataService) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
   }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
    this.search(this.pageNo)
  }
  
     // Search by Date
     searchByDate(){
      if(this.form.value.fromDate > this.form.value.toDate){
        this.service.toastErr('From date should be less  than todate')
        // this.form.value.fromDate = '';
        this.message = "please enter right date"
      }
      else if(this.form.value.fromDate <= this.form.value.toDate){
      
     this.oldDate = new Date(this.form.value.fromDate).toISOString()
        this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
      
      let dummyDateData = {
        "adminId":JSON.parse(localStorage.getItem('adminId')) ,
       "fromDate": this.oldDate,
      "toDate":  this.newDate
      }
      this.service.postApi('api/v1/admin/viewEvent',dummyDateData,'').subscribe((res)=>{
        if(res['responseCode'] == 200){
          this.events = res['result'];
          this.message = res['responseMessage']
        }
        else{
          this.message = res['responseMessage']
   
        }
       
      })
    }
  }
 
  // Reset function 
  Reset(){
   this.searchkey1 = '';
    this.form.reset()
     this.searchByDate()
}
  view(id){
    console.log(id)
    this.router.navigate(['view-events/'+id])
  }
  // Search api 
  search(page){
    this.pageNo = page;
    let postData ={
      search: this.searchkey1,
      pageNumber: this.pageNo,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi(`api/v1/admin/viewEvent`,postData,'').subscribe(response=>{
      if(response['responseCode']==200){
         this.events=response['result'];
         this.paginationData=response['result'];
         this.limit = response['result']['limit']
         this.total=response['result']['total'];
         this.message = response['responseMessage']
      }else{
        this.message = response['responseMessage']
      }
    },error=>{
       this.service.toastErr('Something Went Wrong')
    })
  }

  // For Status Block
  sliderRound(id,status)
  {
  this.status= status
  console.log(id,status)
  if(this.status=='ACTIVE'){

  this.status == "BLOCK"  
  }
  $('#blocked').modal('show')
  console.log(id,status)
  console.log(id,this.status)
  this.userId = id;
  // var Status = status
  this.status = status;                              
  }
  blockCategory(id,status)
  {
  this.status= status
  if (this.status=='BLOCK'){
  this.status = "ACTIVE "
  }
  $('#activate').modal('show')

  // console.log(id,status)
  this.userId = id;
  this.status = status;                              
  }
  
  delete(id){
    this.deleteId = id;
    $('#delete').modal('show');
    console.log('userId>>>>>>>>>>>',this.deleteId )
  }

  deleteUser(){
    let postData={
      adminId : JSON.parse(localStorage.getItem('adminId')),
      eventId: this.deleteId,
      status: 'DELETE'
    }
    this.service.postApi(`api/v1/admin/eventManagement`,postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.search(this.pageNo)
        this.service.showSuccess(response['responseMessage'])
        
      }
      else{
        this.service.toastErr('try again')
      }
    })
  }

  Block(){ 
  this.sliderRound(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
  this.status = 'BLOCK';    
  }
  else{
  this.status = 'ACTIVE';
  }    
  console.log(this.status)


  let apireq = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'eventId':this.userId,
  'status': this.status,
  }
  // console.log('apireq ==>>', apireq)
  this.service.postApi('api/v1/admin/eventManagement',apireq,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#blocked').modal('hide')
    this.service.toastErr(success['responseMessage'])                                         
  this.search(this.pageNo)     
  }
  },error => {
  console.log('Something went wrong');
  })

  }
  // For Status Activate

  Activate(){
  this.blockCategory(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
  this.status = 'BLOCK';    
  }
  else{
  this.status = 'ACTIVE';
  }    
  let req = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'eventId':this.userId,
  }
  this.service.postApi('api/v1/admin/eventManagement',req,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#activate').modal('hide')  
    this.service.showSuccess(success['responseMessage'])                     
  this.search(this.pageNo)     
  }
  },error => {
  console.log('Something went wrong');
  }) 
  }

}
