import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add-auction-category',
  templateUrl: './add-auction-category.component.html',
  styleUrls: ['./add-auction-category.component.css']
})
export class AddAuctionCategoryComponent implements OnInit {
  fileToUpload: File = null;
  obj:any ={};
  form:FormGroup;
  base64Image: any;
  upload: any;
  image : any;
  type: any;
  userId: any;
  categoryName : any;
  fileData: any;
  imageUrl:any='';


  constructor(private service : DataService,private router :Router,private fb : FormBuilder) { }



  ngOnInit() {
    this.form = this.fb.group({
      "file": new FormControl(''),
      "categoryname" : new FormControl('',Validators.compose([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
     });

     this.imageUrl = "assets/img/dummy.png"
  }
  
  // block special character and numbers 
  inputKeyPress(e){
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
      }
  }

  // convert image into base64
  handleFileInput(event) {
    var self = this;
    if(event.target.files && event.target.files[0]){
      var type = event.target.files[0].type;
    this.type=type

    if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
    var reader = new FileReader()
    reader.onload = (e:any)=> {
      if(type === 'image/png' || type === 'image/jpg'){
        self.fileData = e.target['result'].substring(22);
      } 
      else if (type === 'image/jpeg'){
        self.fileData = e.target['result'].substring(23);
      } 
      this.imageUrl = e.target['result']
    }
    reader.readAsDataURL(event.target.files[0]);
    
    
    } else {
    this.service.toastErr("Select only jpg,jpeg and png file.");
    }
    }  

  }
  //'data:'+this.type +'/;base64,'+this.fileData,
  uploadFile() {
   
    let data ={
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "auctionCategoryName": this.form.value.categoryname,
      "categoryType":"CATEGORY",
      "image": this.imageUrl

    }
    this.service.showSpinner()
   this.service.postApi('api/v1/admin/addAuctionCategory',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.service.hideSpinner()
         this.imageUrl = res['categoryData']['image']
         this.userId = res['categoryData']['_id']
         this.categoryName =  res['categoryData']['auctionCategoryName']
        

        this.service.showSuccess('upload Successfully')
        this.router.navigate(['auction-category-management'])
      }
    else  {
      this.service.hideSpinner()
      this.service.toastErr('retry again!')
      }
    },(error)=>{
      this.service.hideSpinner()
      this.service.toastErr('Something went wrong!')
 
    })
   
    }
  

}
