import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-subcategory',
  templateUrl: './edit-subcategory.component.html',
  styleUrls: ['./edit-subcategory.component.css']
})
export class EditSubcategoryComponent implements OnInit {
  user: any
  selectedValue: any = null;
  fileToUpload: File = null;
  obj: any = {};
  form: FormGroup;
  base64Image: any;
  upload: any;
  image: any;
  type: any;
  fileData: any;
  imageUrl: any = '';
  data: any;
  categoryname: any;
  item: any = [];
  subCatID: any;
  subCatName: any;
  categoryId: any;
  // subcatname: any;

  constructor(private service: DataService, private fb: FormBuilder, private router: Router, private activatedroute: ActivatedRoute) { }

  ngOnInit() {
    this.form = this.fb.group({
      "file": new FormControl(''),
      "selectedValue": new FormControl('', Validators.required),
      "subCatName": new FormControl('', ([Validators.required, Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
    });
    this.activatedroute.params.subscribe((params) => {
      this.subCatID = params.id
    })
    this.categoryData()
    this.imagePic()
  }

  inputKeyPress(e) {
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
      }
  }

  handleFileInput(event) {
    var self = this;
    if (event.target.files && event.target.files[0]) {
      var type = event.target.files[0].type;
      this.type = type
      if (type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
        var reader = new FileReader()
        reader.onload = (e: any) => {
          if (type === 'image/png' || type === 'image/jpg') {
            self.fileData = e.target['result'].substring(22);
          }
          else if (type === 'image/jpeg') {
            self.fileData = e.target['result'].substring(23);
          }
          this.imageUrl = e.target['result']

        }
        reader.readAsDataURL(event.target.files[0]);

      } else {
        this.service.toastErr("Select only jpg,jpeg and png file.");
      }
    }
  }

  uploadFile() {
    let postdata = {
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "subCategoryId": this.subCatID,
      "categoryId": this.form.value.selectedValue,
      "subCategoryName": this.form.value.subCatName,
      "image": this.imageUrl
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/editSubCategory', postdata, '').subscribe((res) => {
      if (res['responseCode'] === 200) {
        this.service.hideSpinner()
        this.service.showSuccess('upload Successfully')
        this.router.navigate(['sub-category-management'])
      }
      else {
        this.service.toastErr('retry again!')
      }
    })
  }

  imagePic() {
    this.service.showSpinner()
    let data = {
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "subCategoryId": this.subCatID,
    }
    this.service.postApi('api/v1/admin/viewSubCategory', data, '').subscribe((res) => {
      if (res['responseCode'] = 200) {
        this.imageUrl = res['result']['image']
        this.subCatName = res['result']['subCategoryName'];
        this.selectedValue = res['result']['categoryName'];
        this.categoryId = res['result'].categoryId
        this.form.patchValue({
          selectedValue: this.categoryId,
          subCatName: this.subCatName
        })
        this.service.hideSpinner()
      }
      else {
        this.service.toastErr(res['responseMessage']);
      }
    })
  }
  // For list of category in dropDown
  categoryData() {
    let apireq = {
      "adminId": JSON.parse(localStorage.getItem('adminId')),
    }
    this.service.postApi('api/v1/admin/viewCategory', apireq, '').subscribe((res) => {
      console.log('category data response..', res)
      if (res['responseCode'] == 200) {
        this.item = res['result']['docs']
      }
      else {

        this.service.toastErr(res['responseMessage'])
      }
    })
  }
}
