import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-add-gif',
  templateUrl: './add-gif.component.html',
  styleUrls: ['./add-gif.component.css']
})
export class AddGifComponent implements OnInit {
  fileToUpload: File = null;
  obj:any ={};
  form:FormGroup;
  base64Image: any;
  upload: any;
  image : any;
  type: any;
  fileData: any;
  imageUrl:any='';

  constructor(private service : DataService,private fb :FormBuilder,private router :Router) { }

  ngOnInit() {

    this.form = this.fb.group({
      "file": new FormControl('',Validators.required),
     });
     this.imageUrl = 'assets/img/profile.png'
  }


  handleFileInput(event) {
  
    var self = this;
    if(event.target.files && event.target.files[0]){
      var type = event.target.files[0].type;
    this.type=type

    if(type === 'image/gif') {
    var reader = new FileReader()
    reader.onload = (event:any)=> {
      if(type === 'image/gif'){
        self.fileData = event.target['result'].substring(22);
      } 
      this.imageUrl = event.target['result']
    //  console.log(this.imageUrl)
    }
    reader.readAsDataURL(event.target.files[0]);
    
    } else {
    this.service.toastErr("Select only gif file.");
    
    }
    }  

  }

  uploadFile() {
  
    let data ={
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "gif":  this.imageUrl,

    }
    this.service.showSpinner()
   this.service.postApi('api/v1/admin/addGif',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
       this.service.hideSpinner()
        this.imageUrl = res['gifData']['gif']
        console.log(res['gifData']['_id'])

        this.service.showSuccess(res['responseMessage'])
        this.router.navigate(['/gif-management'])
      }
   
    else  {
      this.service.hideSpinner()
      this.service.toastErr('retry again!')
      }
    },(err)=>{
      this.service.hideSpinner()
    })
   
    }
}
