import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router, NavigationEnd } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-add-industry',
  templateUrl: './add-industry.component.html',
  styleUrls: ['./add-industry.component.css']
})
export class AddIndustryComponent implements OnInit {
  industryName :any;
  form:FormGroup;
  submitted = false;
  constructor(private service:DataService,private router :Router,private fb :FormBuilder) { 
    // router.events.subscribe((event)=>{
    //   console.log(event)
    //   if(event instanceof NavigationEnd){
    //     console.log(event.url.split('/')[1])
    //    }
    // })
  }

  ngOnInit() {
    this.form = this.fb.group({
     industryName  :  new FormControl('',([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
    });
    
  }
   // Block special character and numbers
   inputKeyPress(e){
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
    }
    else {
      document.all ? k = e.keyCode : k = e.which;
      return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
    }
  }
  
  get f() { return this.form.controls; }


  
  AddIndustry(){
    this.submitted = true;
    // stop here if form is invalid
   
    if(this.form.valid){
     let addData = {
       adminId : JSON.parse(localStorage.getItem('adminId')),
       industryType : this.form.value.industryName
     }
     this.service.showSpinner()
     this.service.postApi('api/v1/admin/addIndustry',addData,'').subscribe((res)=>{
       if(res['responseCode'] == 200){
         this.service.hideSpinner()
         this.service.showSuccess(res['responseMessage'])
         this.router.navigate(['/Industry-management'])
       }
       else{
         this.service.hideSpinner()
       }
     },(err)=>{
       this.service.hideSpinner();
       this.service.toastErr('Something went wrong')
     })
  }
 
}
   
 
}

