import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
declare var $:any;
@Component({
  selector: 'app-buying-selling-category',
  templateUrl: './buying-selling-category.component.html',
  styleUrls: ['./buying-selling-category.component.css']
})
export class BuyingSellingCategoryComponent implements OnInit {
  type: any;
  fileData: any;
  form: FormGroup;
  imageUrl: any;

  constructor(private service : DataService,private router : Router,private fb : FormBuilder) {
   
   }

  ngOnInit() {
    this.form = this.fb.group({
      "file": new FormControl('',Validators.required),
      "categoryName" : new FormControl('',([Validators.required,Validators.pattern(/^[^\s][a-zA-Z\s]*$/)]))
     });
     this.imageUrl = "assets/img/dummy.png"
  }
 
  inputKeyPress(e){
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
      }
  }

  handleFileInput(event) {
    var self = this;
    if(event.target.files && event.target.files[0]){
      var type = event.target.files[0].type;
    this.type=type

    if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
    var reader = new FileReader()
    reader.onload = (e:any)=> {
      if(type === 'image/png' || type === 'image/jpg'){
        self.fileData = e.target['result'].substring(22);
      } 
      else if (type === 'image/jpeg'){
        self.fileData = e.target['result'].substring(23);
      } 
      this.imageUrl = e.target['result']
    }
    reader.readAsDataURL(event.target.files[0]);
    
    
    } else {
    this.service.toastErr("Select only jpg,jpeg and png file.");
    }
    }  

  }

  uploadFile() {
   
    let data ={
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "categoryName": this.form.value.categoryName,
      "image":  'data:'+this.type +'/;base64,'+this.fileData,

    }
    this.service.showSpinner()
   this.service.postApi('api/v1/admin/addCategory',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        setTimeout(()=>{
          this.service.hideSpinner()

        },1000)
        this.imageUrl = res['categoryData']['image']
        this.service.showSuccess('upload Successfully')
        this.router.navigate(['buying-selling-category-management'])
      }
   
    else  {
      this.service.toastErr('retry again!')
      }
    })
   
    }
}
