import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';
import { FormGroup, FormControl } from '@angular/forms';
// import { FormGroup } from '../../../../node_modules/@angular/forms';

declare var $: any;

@Component({
  selector: 'app-subadmin-management',
  templateUrl: './sub-adminmanagement.component.html',
  styleUrls: ['./sub-adminmanagement.component.css']
})
export class SubadminManagementComponent implements OnInit {
  pageNo: any = 1;
  searchkey: any = '';
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  user: any = [];
  paginationData: any;
  srNo: number;
  deleteId: any;
  status: any;
  userId: any;
  limit:any;
  total: any;
  response_message: any;
  errormess: any;
  constructor(public router: Router, public service: DataService) { 
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
      "toDate" : new FormControl('')
    })
    this.service.BlockFuture()
    this.search(this.pageNo)
  }
  
  view(id){
    this.router.navigate(['view-subadmin/'+id])
  }

  edit(id){
    this.router.navigate(['edit-subadmin/'+id])
  }
  search(page) {
    this.pageNo = page;
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      search: this.searchkey,
      pageNumber: this.pageNo,
    }
    this.service.postApi(`api/v1/admin/viewSubAdmin`, postData, '').subscribe(response => {
      if (response['responseCode'] == 200) {
        this.response_message = response['responseMessage']
        this.user = response['result'];
        this.paginationData = response['result']
        this.total=response['result']['total']
        this.limit=response['result']['limit']
        this.srNo = (this.pageNo - 1) * this.paginationData.limit
      }
     else{
      this.response_message = response['responseMessage']
     }
     
    },error=>{
      this.service.toastErr('Something Went Wrong')
      })  
  }
 
  Reset(){
    this.searchkey= ''
    this.form.reset()
     this.searchByDate()
}
  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.response_message = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){
    
   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
    
    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewSubAdmin',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.user = res['result'];
        this.response_message = res['responseMessage']
      }
      else{
        this.response_message = res['responseMessage']
 
      }
     
    })
  }
}

  deleteSubAdminFunc(id,status){
    this.deleteId = id;
    this.status = 'DELETE';
    $('#delete').modal('show');
    console.log('User Id===>>',this.deleteId, this.status)
  }

  deleteSubAdmin(){
    let apireq = {
      'adminId':JSON.parse(localStorage.getItem('adminId')),
      'status':this.status,
      'subAdminId':this.deleteId
      }
      this.service.postApi('api/v1/admin/subAdminManagement',apireq,'').subscribe((success:any)=> {
        if (success.responseCode === 200) {
          this.search(this.pageNo)
          $('#delete').modal('hide');
      }
    },error => {
      console.log('Something went wrong');
    })
    }

    sliderRoundFunc(id,status){
      this.status = status
      if(this.status=='ACTIVE'){
      this.status == 'BLOCK';  
      $('#blocked').modal('show')
    
      }
      this.userId = id;
      this.status = status;
     
    }

    blockSubAdminFunc(id,status){
      this.status =status
      if(this.status=='BLOCK'){
        this.status = 'ACTIVE'
        $('#activate').modal('show')

      }
      this.userId =id
      this.status = status

    }
    activateSubAdmin() {
      this.sliderRoundFunc(this.userId,this.status)
      let apireq = {
        'adminId':JSON.parse(localStorage.getItem('adminId')),
        'subAdminId':this.userId,
        'status': 'ACTIVE'
        }
      this.service.postApi('api/v1/admin/subAdminManagement',apireq,'').subscribe((success:any)=> {
        if (success.responseCode === 200) {
          $('#blocked').modal('hide')
          this.status = success['result']['status']
          this.service.showSuccess(success['responseMessage'])         
          this.search(this.pageNo)
          console.log("aaaaaaaaaaaaaaaaaa",this.status)
          
      }
    },error => {
      console.log('Something went wrong');
    })

    }

    blockedSubAdmin(){
       this.blockSubAdminFunc(this.userId,this.status)
      let req = {
        'adminId':JSON.parse(localStorage.getItem('adminId')),
        'subAdminId':this.userId,
        'status':'BLOCK'
        }
      this.service.postApi('api/v1/admin/subAdminManagement',req,'').subscribe((success:any)=> {
        if (success.responseCode === 200) {
          $('#activate').modal('hide')
          this.status = success['result']['status']
          this.service.toastErr(success['responseMessage'])
          this.search(this.pageNo)
          
      }
    },error => {
      console.log('Something went wrong');
    })
    }
    // clear search

clear(){
  this.searchkey = "";
}
  }
