import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-privacy',
  templateUrl: './view-privacy.component.html',
  styleUrls: ['./view-privacy.component.css']
})
export class ViewPrivacyComponent implements OnInit {

  privacyId:any;
  privacy:any=[];
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.search();
  }

  search(){
    let postData={
      type:'PRIVACY',
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/staticPage/staticApi`, postData,'').subscribe(response=>{
      console.log("ytyufregjhskdjgfiasg",response)
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.privacy=response["success"];
      }
    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong')
    })
  }

}
