import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../services/data.service';
declare var $ : any;
@Component({
  selector: 'app-video-gamemanagement',
  templateUrl: './video-gamemanagement.component.html',
  styleUrls: ['./video-gamemanagement.component.css']
})
export class VideoGamemanagementComponent implements OnInit {
 
  pageNo:any=1;
  video:any=[];
  srNo:Number=0;
  searchkey:any;
  paginationData:any;
  deleteId:any;
  status:any;
  gameId:any;
  limit:any;
  total:any;
  userId: any;
  message:any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;

  constructor(public router:Router , public service : DataService) { 
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
    this.search(this.pageNo)
  }
  search(page){
    this.pageNo = page;
    let postData ={
      search: this.searchkey,
      pageNumber: this.pageNo,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi(`api/v1/admin/viewVideo`,postData,'').subscribe(response=>{
      console.log("i am in response ",response)
      if(response['responseCode']==200){
         this.video=response['result'];
         this.message = response['responseMessage']
         this.paginationData=response['result'];
         this.total=response['result']['total'];
         this.pageNo = response['result']['page']
         this.limit = response['result']['limit']
         console.log("check sl no>>>>",this.paginationData ,this.pageNo)
      }else{
        this.message = response['responseMessage']
      }
    },error=>{
       this.service.toastErr('Something Went Wrong')
    })
  }
 
  Reset(){
    this.searchkey = '';
     this.form.reset()
      this.searchByDate()
 }
  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.message = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){
    
   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
    
    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewVideo',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.video = res['result'];
        this.message = res['responseMessage']
      }
      else{
        this.message = res['responseMessage']
 
      }
     
    })
  }
}


  view(id){
    console.log(id)
    this.router.navigate(['view-video/'+id]);
  }

  delete(id){
    this.userId = id
    $('#delete').modal('show');
    console.log('this user edit id====>',id)
   
  }

  deleteUser(){
    // this.delete(id)
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'gameId' : this.userId,
      "status":"DELETE"
    }
    console.log("delete user id ===>",this.userId)
   this.service.postApi('api/v1/admin/videoManagement',postData,'').subscribe((res)=>{
        if(res['responseCode']==200){
          this.service.showSuccess(res['responseMessage'])
          $('#delete').modal('hide');
          this.search(this.pageNo)
       //location.reload()
        }
   }, (error)=>{
     this.service.toastErr('something went wrong')
   })
  }

  sliderRound(id,status)
  {
    this.status= status
    console.log(id,status)
    if(this.status=='ACTIVE'){
   
    this.status = "BLOCK"  
    }
    $('#blocked').modal('show')
    console.log(id,status)
    console.log(id,this.status)
    this.userId = id;
    // var Status = status
    this.status = status;                              
  }
  blockCategory(id,status)
  {
    this.status= status
    if (this.status=='BLOCK'){
      this.status = "ACTIVE "
    }
    $('#activate').modal('show')

    // console.log(id,status)
    this.userId = id;
    this.status = status;                              
  }


  Activate(){ 
    this.sliderRound(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';    
      }
      else{
        this.status = 'ACTIVE';
      }    
    console.log(this.status)
 
 
    let apireq = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'gameId':this.userId,
      'status': this.status,
      }
      // console.log('apireq ==>>', apireq)
    this.service.postApi('api/v1/admin/videoManagement',apireq,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#blocked').modal('hide')
          this.service.toastErr(success['responseMessage'])                                         
        this.search(this.pageNo)     
         }
  },error => {
    console.log('Something went wrong');
  })
 
}

  Block(){
    this.blockCategory(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';    
      }
      else{
        this.status = 'ACTIVE';
      }    
    console.log(this.status)
    let req = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'gameId':this.userId,
      'status': this.status,
      }
    this.service.postApi('api/v1/admin/videoManagement',req,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#activate').modal('hide')  
          this.service.showSuccess(success['responseMessage'])                     
        this.search(this.pageNo)     
    }
  },error => {
    console.log('Something went wrong');
  }) 
  }

}
