import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../services/data.service';
import { all } from 'q';
declare var $: any;
@Component({
  selector: 'app-terms-edit',
  templateUrl: './terms-edit.component.html',
  styleUrls: ['./terms-edit.component.css']
})
export class TermsEditComponent implements OnInit {

  type: any;
  termsView: any;
  editorValue: string;
  form: FormGroup;
  titleData:any;


  config = {
    uiColor: '#F0F3F4',
    height: '100%'
  };
  editData: any;

  constructor(public router: Router, public service: DataService) {


   
  }



  ngOnInit() {

    this.form = new FormGroup({
      "editorValue": new FormControl('', ([Validators.required,Validators.maxLength(515)])),
       "Title": new FormControl('', ([Validators.required, Validators.minLength(3),Validators.pattern(/^[^\s][A-Za-z&\s]*$/)]))
    })
    this.staticApi();
  }
  // Get data from Api
  staticApi() {
    let viewData = {
      type: 'TERMS'
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/staticPage/staticApi', viewData, '').subscribe(response => {
      setTimeout(()=>{
        this.service.hideSpinner();
      },1000)
      if (response['responseCode'] == 200) {
        this.termsView = response["success"]
        this.editData = response["success"]['description'];       
        this.titleData = response["success"]['title'];
        console.log('patch value',this.editData)
        setTimeout(() => {
          this.form.patchValue({
            editorValue: this.editData,
            Title : this.titleData
          });     
        }, 1000);
      }
      else {
        this.service.hideSpinner()
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      this.service.hideSpinner()
      this.service.toastErr('Something Went Wrong')
    })
  }
 // Post data to Api
  editNews() {
    let addData = {
      type: 'TERMS',
      description: this.form.value.editorValue,
      title : this.form.value.Title
    }
     console.log('Add data',addData)
    this.service.showSpinner()
    this.service.postApi('api/v1/staticPage/staticPageUpdate', addData, '').subscribe(response => {

      if (response['responseCode'] == 200) {
        this.service.hideSpinner()
        this.service.showSuccess('Updated successfully')
        this.router.navigate(['static-content-management'])
      }
      else {
        this.service.toastErr(response['responseMessage'])
      }
    }, error => {
      this.service.toastErr('Something Went Wrong')
    })
  }
}
