import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare var $ :any;

@Component({
  selector: 'app-gif-management',
  templateUrl: './gif-management.component.html',
  styleUrls: ['./gif-management.component.css']
})
export class GifManagementComponent implements OnInit {
  user:any;
  pageNo: any = 1;
  paginationData: any;
  srNo: number;
  deleteId: any;
  status: string;
  userId: any;
  limit:any;
  total: any;
  Array: any = [];
  messege: any;
  totalPage: any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;


  constructor(private service: DataService,private router : Router) { 
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture();
    this.listGif(this.pageNo)
  }



          // Search by Date
          searchByDate(){
            if(this.form.value.fromDate > this.form.value.toDate){
              this.service.toastErr('From date should be less  than todate')
              // this.form.value.fromDate = '';
              this.messege = "please enter right date"
            }
            else if(this.form.value.fromDate <= this.form.value.toDate){
            
           this.oldDate = new Date(this.form.value.fromDate).toISOString()
              this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
            
            let dummyDateData = {
              "adminId":JSON.parse(localStorage.getItem('adminId')) ,
             "fromDate": this.oldDate,
            "toDate":  this.newDate
            }
            this.service.postApi('api/v1/admin/viewGif',dummyDateData,'').subscribe((res)=>{
              if(res['responseCode'] == 200){
                this.user = res['result'];
                this.messege = res['responseMessage']
              }
              else{
                this.messege = res['responseMessage']
         
              }
             
            })
          }
        }

  view(id){
    console.log('this user id====>',id)
      this.router.navigate(['view-gif/'+id])
  }

  Reset(){
     this.form.reset()
      this.searchByDate()
 }

  listGif(page){
    this.pageNo = page
    let list = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      pageNumber : this.pageNo,
    }

    this.service.postApi('api/v1/admin/viewGif',list,'').subscribe((res)=>{
       console.log('list res',res)
       if(res['responseCode'] == 200){
         this.user = res['result']
         this.Array = res['result']['docs']
         this.limit =  res['result']['limit']
         this.total  = res['result']['total']
         this.pageNo = res['result']['page']
       }
       else {
         this.service.toastErr(res['responseMessege'])
       }
    })

  }

  delete(id){
    this.userId = id
    $('#delete').modal('show');
  }

     deleteGIF(){
        let postData = {
          adminId : JSON.parse(localStorage.getItem('adminId')),
          'gifId' : this.userId,
          "status" : "DELETE"
        }
     this.service.postApi('api/v1/admin/deleteGif',postData,'').subscribe((res)=>{
        if(res['responseCode']==200){
          this.service.showSuccess(res['responseMessage'])
          $('#delete').modal('hide');
       this.listGif(this.pageNo)
        }
      }, (error)=>{
        this.service.toastErr('something went wrong')
      })
      }

  sliderRound(id,status)
  {
    this.status= status
    console.log(id,status)
    if(this.status=='ACTIVE'){
   
    this.status = "BLOCK"  
    }
    $('#blocked').modal('show')
    console.log(id,status)
    console.log(id,this.status)
    this.userId = id;
    this.status = status;                              
  }
  blockCategory(id,status)
  {
    this.status= status
    if (this.status=='BLOCK'){
      this.status = "ACTIVE "
    }
    $('#activate').modal('show')
    this.userId = id;
    this.status = status;                              
  }


  Activate(){ 
    this.sliderRound(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';    
      }
      else{
        this.status = 'ACTIVE';
      } 

    let apireq = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'gifId':this.userId,
      'status': this.status,
      }
    this.service.postApi('api/v1/admin/deleteGif',apireq,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#blocked').modal('hide')
          this.service.toastErr(success['responseMessage'])                                         
        this.listGif(this.pageNo)     
         }
  },error => {
    console.log('Something went wrong');
  })
 
}

  Block(){
    this.blockCategory(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';    
      }
      else{
        this.status = 'ACTIVE';
      }    
    console.log(this.status)
    let req = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'gifId':this.userId,
      'status': this.status,
      }
    this.service.postApi('api/v1/admin/deleteGif',req,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#activate').modal('hide')  
          this.service.showSuccess(success['responseMessage'])                     
        this.listGif(this.pageNo)     
    }
  },error => {
    console.log('Something went wrong');
  }) 
  }
}
