import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-buying-edit-category',
  templateUrl: './buying-edit-category.component.html',
  styleUrls: ['./buying-edit-category.component.css']
})
export class BuyingEditCategoryComponent implements OnInit {
  userId: any=[];
  user: any;
  categorypic: any;
  categoryName: any;
  data: any = [];
  type :any;
  fileData: any;
  form:FormGroup;
  categoryname: any;

  constructor(private activatedRoute :ActivatedRoute,private service : DataService,private router :Router) { }

  ngOnInit() {
    this.getId()
    this.categoryData()
    this.form = new FormGroup({
      "file" :new FormControl(''),
      "categoryName" : new FormControl('',([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
    })
   
  }
  getId(){
    this.activatedRoute.params.subscribe((params)=>{
      this.userId = params
    })
  } 

  inputKeyPress(e){
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
      }
  }

  handleFileInput(event) {
   var self = this;
   if(event.target.files && event.target.files[0]){
     var type = event.target.files[0].type;
   this.type=type

   if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
   var reader = new FileReader()
   reader.onload = (e:any)=> {
     if(type === 'image/png' || type === 'image/jpg'){
       self.fileData = e.target['result'].substring(22);
     } 
     else if (type === 'image/jpeg'){
       self.fileData = e.target['result'].substring(23);
     } 
     this.categorypic = e.target['result']
   }
   reader.readAsDataURL(event.target.files[0]);
   
   
   } else {
   this.service.toastErr("Select only jpg,jpeg and png file.");
   }
   }  
// console.log('data:',this.type +'/;base64,'+this.fileData)
 }

 categoryData(){
   let catData = {
    adminId : JSON.parse(localStorage.getItem('adminId')),
    categoryId : this.userId.id
   }
   this.service.showSpinner()
   this.service.postApi('api/v1/admin/viewCategory',catData,'').subscribe((res)=>{
    setTimeout(()=>{
      this.service.hideSpinner()
  
    },1000)
     this.categoryname = res['result']['categoryName'];
     this.categorypic = res['result']['image']
     this.form.patchValue({
      categoryName : this.categoryname
    
     });
   
   })

 }
 editCategory(){ 
let postData = {
adminId : JSON.parse(localStorage.getItem('adminId')),
categoryId : this.userId.id,
categoryName : this.form.value.categoryName,
image  : this.categorypic
}
this.service.showSpinner()
this.service.postApi('api/v1/admin/editCategory',postData,'').subscribe((res)=>{
if(res['responseCode'] == 200){
  setTimeout(()=>{
    this.service.hideSpinner()

  },1000)
 this.service.showSuccess('updated Succefully')
 this.router.navigate(['buying-selling-category-management'])
}
else  {
 this.service.toastErr('retry again!')
 }
})
}


}
