import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare var $ :any;
@Component({
  selector: 'app-discussion-forum-management',
  templateUrl: './discussion-forum-management.component.html',
  styleUrls: ['./discussion-forum-management.component.css']
})
export class DiscussionForumManagementComponent implements OnInit {
  pageNo:any=1;
  class:any=[];
  srNo:Number=0;
  searchkey:any;
   searchkey1:any;
  paginationData:any;
  deleteId:any;
  status:any;
  classId:any;
  limit:any;
  total:any;
  userId: any;
  message: any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;

  constructor(public router:Router , public service : DataService) { }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture();
     this.search(this.pageNo)
  }

  Reset(){
    this.searchkey = '';
     this.form.reset()
      this.searchByDate()
 }
  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.message = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){

   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')

    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewDiscussionForm',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.class = res['result'];
        this.message = res['responseMessage']
      }
      else{
        this.message = res['responseMessage']

      }

    })
  }
}


  search(page){
    this.pageNo = page;
    let postData ={
      search: this.searchkey,
      pageNumber: this.pageNo,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi(`api/v1/admin/viewDiscussionForm`,postData,'').subscribe(response=>{
      if(response['responseCode']==200){
         this.class=response['result'];
         this.paginationData=response['result'];
         this.total=response['result']['total'];
         this.pageNo = response['result']['page'];
         this.limit = response['result']['limit']
         this.message = response['responseMessage']
        //  console.log("check sl no>>>>",this.paginationData ,this.pageNo)
      }else{
        this.message = response['responseMessage']
      }
    },error=>{
       this.service.toastErr('Something Went Wrong')
    })
  }

  search1(page){
    this.pageNo = page;
    let postData ={
      name: this.searchkey1,
      pageNumber: this.pageNo,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi('api/v1/admin/searchDiscussionByCreatar',postData,'').subscribe((res)=>{
      if(res['responseCode']==200){
        this.class=res['result'];
        this.paginationData=res['result'];
        this.service.showSuccess(res['responseMessage'])
        this.total=res['TotalPage'];
        this.limit = res['limit']
        console.log("check sl no>>>>",this.paginationData ,this.class, this.pageNo, this.total)
     }else{
       this.service.toastErr(res['responseMessage'])
     }
   },error=>{
      this.service.toastErr('Something Went Wrong')
   })

  }

  view(id){
    console.log(id)
    this.router.navigate(['view-discussion/'+id]);
  }

  // edit(id){
  //   console.log(id)
  //   this.router.navigate([])
  // }
  // deleteJob(id,status){
  //   this.deleteId = id;
  //   this.status='DELETE';
  //   $('#delete').modal('show');
  //   console.log('userId>>>>>>>>>>>',this.deleteId , this.status)
  // }


  sliderRound(id,status)
  {
  this.status= status
  console.log(id,status)
  if(this.status=='ACTIVE'){

  this.status == "BLOCK"
  }
  $('#blocked').modal('show')
  console.log(id,status)
  console.log(id,this.status)
  this.userId = id;
  // var Status = status
  this.status = status;
  }
  blockCategory(id,status)
  {
  this.status= status
  if (this.status=='BLOCK'){
  this.status = "ACTIVE "
  }
  $('#activate').modal('show')

  // console.log(id,status)
  this.userId = id;
  this.status = status;
  }


  Block(){
  this.sliderRound(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
  this.status = 'BLOCK';
  }
  else{
  this.status = 'ACTIVE';
  }
  console.log(this.status)


  let apireq = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'disscussionId':this.userId,
  'status': this.status,
  }
  // console.log('apireq ==>>', apireq)
  this.service.postApi('api/v1/admin/discussionFormManagement',apireq,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#blocked').modal('hide')
    this.service.toastErr('Discussion forum blocked successfully')
  this.search(this.pageNo)
  }
  },error => {
  console.log('Something went wrong');
  })

  }

  Activate(){
  this.blockCategory(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
  this.status = 'BLOCK';
  }
  else{
  this.status = 'ACTIVE';
  }
  console.log(this.status)
  let req = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'disscussionId':this.userId,
  }
  this.service.postApi('api/v1/admin/discussionFormManagement',req,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#activate').modal('hide')
    this.service.showSuccess('Discussion forum activated successfully')
  this.search(this.pageNo)
  }
  },error => {
  console.log('Something went wrong');
  })
  }

  delete(id){
    this.userId = id
    $('#delete').modal('show');
    console.log('this user edit id====>',id)

  }

  deleteUser(){
    // this.delete(id)
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'disscussionId' : this.userId,
      "status":"DELETE"
    }
    console.log("delete user id ===>",this.userId)
   this.service.postApi('api/v1/admin/discussionFormManagement',postData,'').subscribe((res)=>{
        if(res['responseCode']==200){
          this.service.showSuccess(res['responseMessage'])
          $('#delete').modal('hide');
          this.search(this.pageNo)
       //location.reload()
        }
   }, (error)=>{
     this.service.toastErr('something went wrong')
   })
  }

}
