import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-discussion',
  templateUrl: './view-discussion.component.html',
  styleUrls: ['./view-discussion.component.css']
})
export class ViewDiscussionComponent implements OnInit {
  discussionId: any;
  user: any;
  userName: any;

  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewDiscussion()
  }
  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.discussionId=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.discussionId,"KKKKKKKKKK",params)
    })
}

viewDiscussion(){
  let postData={
    discussionId:this.discussionId,
    adminId : JSON.parse(localStorage.getItem('adminId'))
  } 
  this.service.postApi(`api/v1/admin/viewDiscussionForm`, postData,'').subscribe((response):any=>{
    if(response['responseCode'] == 200){
      this.user=response['result'];
      this.userName = response['result']['userId']['firstName']
      console.log("to check userDetails", this.user)
    }
  })
}
}