import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../services/data.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-auction-category',
  templateUrl: './edit-auction-category.component.html',
  styleUrls: ['./edit-auction-category.component.css']
})
export class EditAuctionCategoryComponent implements OnInit {
  userId: any = [];
  user: any;
  categorypic: any;
  categoryName: any;
  data: any = [];
  type: any;
  fileData: any;
  form: FormGroup;

  constructor(private activatedRoute: ActivatedRoute, private service: DataService, private router: Router) { }

  ngOnInit() {
    this.getId()
    this.listCategory()
    this.form = new FormGroup({
      "file": new FormControl(''),
      "categoryName": new FormControl('', ([Validators.required, Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
    })
  }


  getId() {
    this.activatedRoute.params.subscribe((params) => {
      this.userId = params
    })
  }

  inputKeyPress(e) {
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
      }
      else{
        document.all ? k = e.keyCode : k = e.which;
        return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
      }
  }

  handleFileInput(event) {
    var self = this;
    if (event.target.files && event.target.files[0]) {
      var type = event.target.files[0].type;
      this.type = type

      if (type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
        var reader = new FileReader()
        reader.onload = (e: any) => {
          if (type === 'image/png' || type === 'image/jpg') {
            self.fileData = e.target['result'].substring(22);
          }
          else if (type === 'image/jpeg') {
            self.fileData = e.target['result'].substring(23);
          }
          this.categorypic = e.target['result']
        }
        reader.readAsDataURL(event.target.files[0]);


      } else {
        this.service.toastErr("Select only jpg,jpeg and png file.");
      }
    }

  }
  editCategory() {
    let postData = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      auctionCategoryId: this.userId.id,
      image: this.categorypic,
      auctionCategoryName: this.form.value.categoryName

    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/editAuctionCategory', postData, '').subscribe((res) => {
      if (res['responseCode'] == 200) {
        this.service.hideSpinner()
        this.service.showSuccess('updated Succefully')
        this.router.navigate(['auction-category-management'])
      }
      else {
        this.service.hideSpinner()
        this.service.toastErr(res['message'])
      }
    }, (err) => {
      this.service.toastErr('Something went wrong')
    })
  }

  listCategory() {
    let postData1 = {
      adminId: JSON.parse(localStorage.getItem('adminId')),
      auctionCategoryId: this.userId.id,
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewAuctionCategory', postData1, '').subscribe((res) => {
      if (res['responseCode'] == 200) {
        setTimeout(() => {
          this.service.hideSpinner()
        }, 1000);
        this.categoryName = res['result']['auctionCategoryName'];
        this.categorypic = res['result']['image']
        this.form.patchValue({
          categoryName: this.categoryName
        })
      }
      else {
        this.service.toastErr('retry again!')
      }
    })
  }

}
