import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../services/data.service';
declare var $:any;
@Component({
  selector: 'app-news-management',
  templateUrl: './news-management.component.html',
  styleUrls: ['./news-management.component.css']
})
export class NewsManagementComponent implements OnInit {

  pageNo:any=1;
  news:any=[];
  srNo:Number=0;
  searchkey:any;
  paginationData:any;
  deleteId:any;
  status:any;
  newsId:any;
  limit:any;
  total:any;
  userId: any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  messege: string;


  constructor(public router:Router , public service : DataService) { }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.search(this.pageNo);
    this.service.BlockFuture()
  }
  search(page){
    this.pageNo = page;
    let postData ={
      search: this.searchkey,
      pageNumber: this.pageNo,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi(`api/v1/admin/viewNews`,postData,'').subscribe(response=>{
      console.log("i am in response ",response)
      if(response['responseCode']==200){
         this.news=response['result1'];
         this.paginationData=response['result1'];
         this.total=response['result1']['total'];
         this.limit = response['result1']['limit'];
         this.messege = response['responseMessage']
      }else{
        this.messege = response['responseMessage']
      }
    },error=>{
       this.service.toastErr('Something Went Wrong')
    })
  }

  // Search by Date
searchByDate(){
  if(this.form.value.fromDate > this.form.value.toDate){
    this.service.toastErr('From date should be less  than todate')
    // this.form.value.fromDate = '';
    this.messege = "please enter right date"
  }
  else if(this.form.value.fromDate <= this.form.value.toDate){
  
 this.oldDate = new Date(this.form.value.fromDate).toISOString()
    this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
  
  let dummyDateData = {
    "adminId":JSON.parse(localStorage.getItem('adminId')) ,
   "fromDate": this.oldDate,
  "toDate":  this.newDate
  }
  this.service.postApi('api/v1/admin/viewNews',dummyDateData,'').subscribe((res)=>{
    if(res['responseCode'] == 200){
      this.news = res['result1'];
      this.paginationData = res['result1']
      this.limit = res['result1']['limit']
       this.total=res['result1']['total'];
       this.pageNo = res['result1']['page']
      this.messege = res['responseMessage']
    }
    else{
      this.messege = res['responseMessage']

    }
   
  })
}
}

Reset(){
  this.searchkey= ''
  this.form.reset()
   this.searchByDate()
}

  view(id){
    console.log(id)
    this.router.navigate(['view-news/'+id]);
  }

  edit(id){
    console.log(id)
    this.router.navigate(['edit-news/'+id])
  }
  // deleteJob(id,status){
  //   this.deleteId = id;
  //   this.status='DELETE';
  //   $('#delete').modal('show');
  //   console.log('userId>>>>>>>>>>>',this.deleteId , this.status)
  // }

  deleteUserFunc(id,status){
    this.deleteId = id;
    $('#delete').modal('show');
    console.log('User Id===>>',this.deleteId, this.status)
  }

  

  deleteUser(){
    let apireq = {
      newsId:this.deleteId,
      status: 'DELETE',
      adminId : JSON.parse(localStorage.getItem('adminId'))
      }
      console.log('apireq ==>>', apireq)
      this.service.postApi('api/v1/admin/newsManagement',apireq,'').subscribe((success:any)=> {
        console.log(success)
        if (success.responseCode === 200) {
          this.search(this.pageNo)
          $('#delete').modal('hide');
      }
    },error => {
      console.log('Something went wrong',error);
    })
    }
 
    sliderRound(id,status)
    {
      this.status= status
      console.log(id,status)
      if(this.status=='ACTIVE'){
     
      this.status = "BLOCK"  
      }
      $('#blocked').modal('show')
      console.log(id,status)
      console.log(id,this.status)
      this.userId = id;
      // var Status = status
      this.status = status;                              
    }
    blockCategory(id,status)
    {
      this.status= status
      if (this.status=='BLOCK'){
        this.status = "ACTIVE "
      }
      $('#activate').modal('show')
  
      // console.log(id,status)
      this.userId = id;
      this.status = status;                              
    }
  
  
    Activate(){ 
      this.sliderRound(this.userId,this.status)
      console.log('this is sthe id and status',this.userId,this.status)
      if(this.status=='ACTIVE'){
        this.status = 'BLOCK';    
        }
        else{
          this.status = 'ACTIVE';
        }    
      console.log(this.status)
   
   
      let apireq = {
        adminId : JSON.parse(localStorage.getItem('adminId')),
        'newsId':this.userId,
        'status': this.status,
        }
        // console.log('apireq ==>>', apireq)
      this.service.postApi('api/v1/admin/newsManagement',apireq,'').subscribe((success:any)=> {
        console.log(success)
        if (success.responseCode === 200) {
          $('#blocked').modal('hide')
            this.service.toastErr('News deactivated successfully')                                         
          this.search(this.pageNo)     
           }
    },error => {
      console.log('Something went wrong');
    })
   
  }
  
    Block(){
      this.blockCategory(this.userId,this.status)
      console.log('this is sthe id and status',this.userId,this.status)
      if(this.status=='ACTIVE'){
        this.status = 'BLOCK';    
        }
        else{
          this.status = 'ACTIVE';
        }    
      console.log(this.status)
      let req = {
        adminId : JSON.parse(localStorage.getItem('adminId')),
        'newsId':this.userId,
        'status': this.status,
        }
      this.service.postApi('api/v1/admin/newsManagement',req,'').subscribe((success:any)=> {
        console.log(success)
        if (success.responseCode === 200) {
          $('#activate').modal('hide')  
            this.service.showSuccess('News activated successfully')                     
          this.search(this.pageNo)     
      }
    },error => {
      console.log('Something went wrong');
    }) 
    }

  logout(){
    $('#logout_modal').modal('hide');
    this.router.navigate(['login'])
  }

}
