import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';
declare var $ : any;
@Component({
  selector: 'app-static-content-management',
  templateUrl: './static-content-management.component.html',
  styleUrls: ['./static-content-management.component.css']
})
export class StaticContentManagementComponent implements OnInit {
type:any;
  result: any;
  successData: any;
  description={term:'',privacy:'',about:'',help:''}
  title: any;
  privacy: any;
  aboutus: any;
  help:any;
  constructor(public router: Router, public service: DataService) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
   }

  ngOnInit() {
    this.viewTermsAndConditions();
    this.viewPrivacyPolicy();
    this.viewAboutUs();
    this.viewHelp();
  }

  onTermsEditClick(){
    this.router.navigate(['terms-edit'])
  }

  onPrivacyEditClick(){
    this.router.navigate(['privacy-edit'])
  }
  onAboutUsEditClick(){
    this.router.navigate(['about-edit'])
  }
  onHelpEditClick(){
    this.router.navigate(['help-edit'])
  }
  viewTermsAndConditions(){
    let apireq = {
      type: 'TERMS'  
      }
      console.log('apireq ==>>', apireq)
      this.service.postApi('api/v1/staticPage/staticApi',apireq,'').subscribe((success:any)=> {
        console.log(success)
        this.successData=success;
        if (success.responseCode === 200) {
          this.description.term=success.success.description
          this.title = success.success.title
          if(this.description.term.length>50){
            this.description.term=this.description.term.slice(0,100)+'...'
          }
         
          localStorage.setItem('terms',JSON.stringify(this.successData.success._id));
          
      }
    },error => {
      console.log('Something went wrong');
    })
  }

  viewPrivacyPolicy(){
    let apireq = {
      type: 'PRIVACY'  
      }
      console.log('apireq ==>>', apireq)
      this.service.postApi('api/v1/staticPage/staticApi',apireq,'').subscribe((success:any)=> {
        console.log(success)
        this.successData=success;
        if (success.responseCode === 200) {
          this.description.privacy=success.success.description;
          this.privacy = success.success.title
          if(this.description.privacy.length>50){
            this.description.privacy=this.description.privacy.slice(0,100)+'...'
          }
            localStorage.setItem('privacy',JSON.stringify(this.successData.success._id));
      }
    },error => {
      console.log('Something went wrong');
    })
  }
  viewAboutUs(){
    let apireq = {
      type: 'ABOUT_US'  
      }
      console.log('apireq ==>>', apireq)
      this.service.postApi('api/v1/staticPage/staticApi',apireq,'').subscribe((success:any)=> {
        console.log(success)
        this.successData=success;
        if (success.responseCode === 200) {
          this.description.about=success.success.description
          this.aboutus = success.success.title
          if(this.description.about.length>50){
            this.description.about=this.description.about.slice(0,100)+'...'
          }
            localStorage.setItem('about',JSON.stringify(this.successData.success._id));
      }
    },error => {
      console.log('Something went wrong');
    })
  }
  viewHelp(){
    let apireq = {
      type: 'HELP'  
      }
      console.log('apireq ==>>', apireq)
      this.service.postApi('api/v1/staticPage/staticApi',apireq,'').subscribe((success:any)=> {
        console.log(success)
        this.successData=success;
        if (success.responseCode === 200) {
          this.description.help=success.success.description;
          this.help = success.success.title
          if(this.description.help.length>50){
            this.description.help=this.description.help.slice(0,100)+'...'
          }
            localStorage.setItem('help',JSON.stringify(this.successData.success._id));
      }
    },error => {
      console.log('Something went wrong');
    })
  }


  
}
