import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { HeaderComponent } from '../header/header.component';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnInit {
  list:any = [];
  count: any;
  url:any;
  seen: boolean = false;
  deleteId: any;
  constructor(private service: DataService,private header : HeaderComponent,
    private spinner : NgxSpinnerService,private router : Router) {
      this.url = window.location.href.split('/')
       this.router.events.subscribe((ev) => {
        if (ev instanceof NavigationEnd) {
          console.log(ev);
          if(this.url[3]=='notification'){
            this.header.count = 0;
            }


          /* Your code goes here on every router change */}
      });
   }

  ngOnInit() {
   this.getNotification()
  }

  getNotification(){
    this.service.getNotificationList()
    setTimeout(() => {
      this.list = this.service.list
      console.log('list',this.list);
     if(this.url[3]=='notification'){
       this.header.count = 0;
       console.log('header calling...',this.header.count)
       }
       }, 1000);
      }

      clear(id){
        this.deleteId =id;
        console.log('delete id',this.deleteId);
        let data = {
          notificationId : this.deleteId,
          adminId : JSON.parse(localStorage.getItem('adminId')),

        };
        this.spinner.show()
        this.service.postApi('api/v1/admin/deleteNotification',data,'').subscribe((res:any)=>{
          this.spinner.hide()
          if(res.responseCode == 200){
            console.log('success delete');
           this.service.getNotificationList();
           this.getNotification()
          }
        },(err)=>{
          this.spinner.hide()
        })
      }
}
