  getId(){
    
    this.activatedRoute.queryParams.subscribe((params)=>{
      // this.id=params.id
      this.idArray = JSON.parse(params.value);
      this.id = this.idArray.jobId;
      this.appicantId = this.idArray.applicantId
      console.log("dfmdfs",this.idArray,"jobid",this.id,"applicantId",this.appicantId,"KKKKKKKKKK",JSON.parse(params.value) );
      console.log("applicantId",this.appicantId);

    })
  }

  getAllAplicant(page){
    this.pageNo = page
     this.data = {
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.showSpinner()
     this.appicantId.forEach(ele => {
       this.data['userId'] = ele;
       this.service.postApi(`api/v1/admin/viewApplicant`, this.data,'').subscribe(response=>{
         this.service.hideSpinner()
        if(response['responseCode']==200){
          this.userData.push(response['result']);
          // this.total = this.userData.length
          console.log("to check userDetails", this.userData,response['result'])
        }
      },(err)=>{
        this.service.hideSpinner()
      })
       console.log('userData',this.userData,'data object',this.data)

     });
  }
  
