  viewjob(){
    let postData={
      jobId:this.id,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    } 
    this.service.postApi(`api/v1/admin/viewJob`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.user=response['results'][0];
        this.jobPic = response['results'][0]
        console.log("to check userDetails", this.user)
      }
    })
  }

  

