  blockCategory(id,applicantStatus)
  {
  this.status= applicantStatus

  $('#activate').modal('show')

   console.log(id,this.status)
  this.userId = id;
  // this.status = applicantStatus;                              
  }


  Block(){ 
  this.sliderRound(this.userId,this.status)
  if(this.status=='ACTIVE'){
    this.status ='INACTIVE';    
    }
    else{
    this.status = 'ACTIVE';
    }    


  let apireq = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'appliedJobId':this.userId,
  'applicantStatus': this.status,
  }

  this.service.postApi('api/v1/admin/applicantManagement',apireq,'').subscribe((success:any)=> {

  if (success.responseCode === 200) {
    this.userData = [];
    this.getAllAplicant(this.pageNo)
  $('#blocked').modal('hide');
    this.service.toastErr('Applicant inactivated successfully')                                         
  }
  },error => {
    this.service.toastErr('Something went wrong')                                         
  })

  }

  Activate(){
  this.blockCategory(this.userId,this.applicantStatus)
  if(this.status=='ACTIVE'){
    this.status = 'INACTIVE';    
    }
    else{
    this.status = 'ACTIVE';
    }    
  let req = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'appliedJobId':this.userId
  }

  this.service.postApi('api/v1/admin/applicantManagement',req,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
    this.userData = [];
    this.getAllAplicant(this.pageNo)
  $('#activate').modal('hide')  ;
  this.service.showSuccess('Applicant activated successfully')                     
  }
  },error => {
    this.service.toastErr('Something went wrong')                                         
  }) 
  }
  
