import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-auction-category',
  templateUrl: './view-auction-category.component.html',
  styleUrls: ['./view-auction-category.component.css']
})
export class ViewAuctionCategoryComponent implements OnInit {
  userId :any;
  user:any;
  categorypic :any = " ";

  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewCategory()
  }



  getId(){
    this.activatedRoute.params.subscribe((params)=> {
     this.userId = params
    })
  }
  
  viewCategory(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     auctionCategoryId : this.userId.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewAuctionCategory',postData,'').subscribe((res)=>{
      if(res['responseCode']== 200){
        setTimeout(() => {
          this.service.hideSpinner()
        }, 1000);
        this.user= res['result']
        this.categorypic = res['result']['image']

      }
      else {
        this.service.hideSpinner()
        this.service.toastErr( res['responseMessage'])
      }
    },(err)=>{
      this.service.hideSpinner()
    })
  }
}
