import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-advertise',
  templateUrl: './view-advertise.component.html',
  styleUrls: ['./view-advertise.component.css']
})
export class ViewAdvertiseComponent implements OnInit {
  userId: any;
  user: any;
  Profitpic: any;
  clickby: any = [];
  likes: any = [];
  comments: any = [];
  sharedBy: any= [];
  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
    this.Profitpic = "assets/img/imageplace.jpeg"
  }
  getId(){
    this.activatedRoute.params.subscribe((params)=> {

     this.userId = params
        console.log("Object=-=-=---=-->",this.userId)
        console.log(params)
    })
  }

  viewUser(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     advertisementId : this.userId.id
    };
     this.user = [];
    // this.user = {
    //   image : ["https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio", "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio",
    //   "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio", "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio",
    //   "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio", "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio",
    //   "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio", "https://res.cloudinary.com/dl2d0v5hy/raw/upload/v1579338392/cagh0iflfdf9aopmleio"]
    // }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewAdv',postData,'').subscribe((res)=>{
      this.service.hideSpinner()
      if(res['responseCode']== 200){
               this.user= res['result'];
               this.clickby = res['result']['clickBy'];
               this.likes = res['result']['likes']
               this.comments = res['result']['comments'];
               this.sharedBy = res['result']['sharedBy']
      }
      else{
        this.service.toastErr(res['responseMessage'])

      }
    },(err)=>{
      this.service.hideSpinner()
      this.service.toastErr('Please try again')
    })
  }
}
