import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-help',
  templateUrl: './view-help.component.html',
  styleUrls: ['./view-help.component.css']
})
export class ViewHelpComponent implements OnInit {

  privacyId:any;
  help:any=[];
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.gethelp()
  }
 
  gethelp(){
    let postData={
      type:'HELP',
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/staticPage/staticApi`, postData,'').subscribe(response=>{
      console.log("type help res",response)
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.help=response["success"];
        console.log('help',this.help)
      }
    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong')
    })
  }
}
