import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-buying-view-category',
  templateUrl: './buying-view-category.component.html',
  styleUrls: ['./buying-view-category.component.css']
})
export class BuyingViewCategoryComponent implements OnInit {
  userId :any;
  user:any;
  categoryPic :any;

  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
  }
  getId(){
    this.activatedRoute.params.subscribe((params)=> {
    
     this.userId = params
        console.log("Object=-=-=---=-->",this.userId)
        console.log(params)
    })
  }
  
  viewUser(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     categoryId : this.userId.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewCategory',postData,'').subscribe((res)=>{
      setTimeout(()=>{
        this.service.hideSpinner()
    
      },1000)
      if(res['responseCode']== 200){
        console.log('responsee',res)
        this.user= res['result']
        this.categoryPic = res['result']['image']

      }
    })
  }
}
