import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-group',
  templateUrl: './view-group.component.html',
  styleUrls: ['./view-group.component.css']
})
export class ViewGroupComponent implements OnInit {

  grouId:any;
  user:any=[];
  memberName:any;
  Posts: any = [];
  totalImages: any;
  totalLikes: any;
  totalSharedBy: any;
  totalComments: any;
  totalVideo: any;
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.search();
  }

  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.grouId=params.id
    })
  }
  search(){
    let postData={
      groupId:this.grouId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewGroup`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.user=response['result'];
        this.Posts = response['allPost'];
           this.totalImages = this.Posts.reduce((rx, i) => rx + i.image.length, 0);
           this.totalVideo =this.Posts.reduce((rx, i) => rx + i.video.length, 0)
           this.totalLikes = this.Posts.reduce((rx, i) => rx + i.likes.length, 0)



      }
    })
  }

}
