import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';
declare var $ : any;
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  headerData: any;
  data: any;

  constructor(public router :Router,private service : DataService) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
   }

  ngOnInit() {
    this.dashboardDynamicData();
  }





  logoutFunc(){
    $('#logout_modal').modal('hide');
    this.router.navigate(['login'])
  }
  logoutModal() {
    $('#logout_modal').modal({backdrop:"static",keyboard:false})

  }

  dashboardDynamicData(){
    this.service.getApi('api/v1/admin/dashboardMng','').subscribe((res:any)=>{
      if(res.responseCode == 200){
         this.data = res
         console.log('data',this.data)
      }
    })
  }

}
