import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare var $ :any;

@Component({
  selector: 'app-sticker-management',
  templateUrl: './sticker-management.component.html',
  styleUrls: ['./sticker-management.component.css']
})
export class StickerManagementComponent implements OnInit {
  user:any;
  pageNo: any = 1;
  paginationData: any;
  srNo: number;
  deleteId: any;
  status: string;
  userId: any;
  limit:any;
  total: any;
  Array: any = [];
  messege: any;
  totalPage: any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;


  constructor(private service: DataService,private router : Router) { }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
    this.listSticker(this.pageNo)
  }
  
  Reset(){
    this.form.reset()
     this.searchByDate()
}
  // Search by Date
  searchByDate(){
    if(this.form.value.fromDate > this.form.value.toDate){
      this.service.toastErr('From date should be less  than todate')
      // this.form.value.fromDate = '';
      this.messege = "please enter right date"
    }
    else if(this.form.value.fromDate <= this.form.value.toDate){
    
   this.oldDate = new Date(this.form.value.fromDate).toISOString()
      this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
    
    let dummyDateData = {
      "adminId":JSON.parse(localStorage.getItem('adminId')) ,
     "fromDate": this.oldDate,
    "toDate":  this.newDate
    }
    this.service.postApi('api/v1/admin/viewSticker',dummyDateData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.user = res['result'];
        this.messege = res['responseMessage']
      }
      else{
        this.messege = res['responseMessage']
 
      }
     
    })
  }
}
  

  view(id){
    console.log('this user id====>',id)
      this.router.navigate(['view-sticker/'+id])
  }

  listSticker(page){
    this.pageNo = page
    let list = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      pageNumber : this.pageNo,
    }
    this.service.postApi('api/v1/admin/viewSticker',list,'').subscribe((res)=>{
       console.log('list res',res)
       if(res['responseCode'] == 200){
         this.user = res['result']
         this.Array = res['result']['docs']
         this.pageNo = res['result']['page']
         this.limit = res['result']['limit']
         this.total = res['result']['total'];
         this.messege = res['responseMessage']
       
       }
       else {
        this.messege = res['responseMessage']
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })

  }

  delete(id){
    this.userId = id
    $('#delete').modal('show');
    console.log('this user edit id====>',id)
   
  }

  deleteSticker(){
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'stickerId' : this.userId,
      'status' : 'DELETE'
    }
    console.log("delete user id ===>",this.userId)
   this.service.postApi('api/v1/admin/stickerManagment',postData,'').subscribe((res)=>{
        if(res['responseCode']==200){
          this.service.showSuccess(res['responseMessage'])
          $('#delete').modal('hide');
       this.listSticker(event)
        }
   }, (error)=>{
     this.service.toastErr('something went wrong')
   })
  }

  sliderRound(id,status)
  {
    this.status= status
    console.log(id,status)
    if(this.status=='ACTIVE'){
   
    this.status = "BLOCK"  
    }
    $('#blocked').modal('show')
    console.log(id,status)
    console.log(id,this.status)
    this.userId = id;
    // var Status = status
    this.status = status;                              
  }
  blockCategory(id,status)
  {
    this.status= status
    if (this.status=='BLOCK'){
      this.status = "ACTIVE "
    }
    $('#activate').modal('show')

    // console.log(id,status)
    this.userId = id;
    this.status = status;                              
  }


  Activate(){ 
    this.sliderRound(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';    
      }
      else{
        this.status = 'ACTIVE';
      }    
    console.log(this.status)
 
 
    let apireq = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'stickerId':this.userId,
      'status': this.status,
      }
      // console.log('apireq ==>>', apireq)
    this.service.postApi('api/v1/admin/stickerManagment',apireq,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#blocked').modal('hide')
          this.service.toastErr(success['responseMessage'])                                         
        this.listSticker(event)     
         }
  },error => {
    console.log('Something went wrong');
  })
 
}

  Block(){
    this.blockCategory(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';    
      }
      else{
        this.status = 'ACTIVE';
      }    
    console.log(this.status)
    let req = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'stickerId':this.userId,
      'status': this.status,
      }
    this.service.postApi('api/v1/admin/stickerManagment',req,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#activate').modal('hide')  
          this.service.showSuccess(success['responseMessage'])                     
        this.listSticker(event)     
    }
  },error => {
    console.log('Something went wrong');
  }) 
  }
}
