import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-video',
  templateUrl: './view-video.component.html',
  styleUrls: ['./view-video.component.css']
})
export class ViewVideoComponent implements OnInit {

  gameId:any;
  game:any;
  imageUrl : any;
  // memberName:any;
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.viewVideo();
    this.imageUrl = 'assets/img/videoPlace.png'
  }

  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.gameId=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.gameId,"KKKKKKKKKK",params)
    })
  }
  viewVideo(){
    let postData={
      gameId:this.gameId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewVideo`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.game=response['result'];
      }
      else{
        this.service.hideSpinner()
      }
    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong');
    })
  }

}
