import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-non-profit',
  templateUrl: './view-non-profit.component.html',
  styleUrls: ['./view-non-profit.component.css']
})
export class ViewNonProfitComponent implements OnInit {
  userId: any;
  user: any;
  Profitpic: any;

  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
    this.Profitpic = "assets/img/imageplace.jpeg"
    
  }
  getId(){
    this.activatedRoute.params.subscribe((params)=> {
    
     this.userId = params
        console.log("Object=-=-=---=-->",this.userId)
        console.log(params)
    })
  }

  viewUser(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     eventId : this.userId.id
    }
    this.service.postApi('api/v1/admin/viewNonProfitEvent',postData,'').subscribe((res)=>{
      if(res['responseCode']== 200){
        console.log('responsee',res)
        this.user= res['result']
        this.Profitpic = res['result']['image']

      }
    })
  }
}
