import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-subcategory',
  templateUrl: './view-subcategory.component.html',
  styleUrls: ['./view-subcategory.component.css']
})
export class ViewSubcategoryComponent implements OnInit {
  userId :any;
  user:any;
  subcategorypic :any = " ";
  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
  }

  getId(){
    this.activatedRoute.params.subscribe((params)=> {
     this.userId = params
        console.log("Object=-=-=---=-->",this.userId)
      })
     }
  
  viewUser(){
    this.service.showSpinner()
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     subCategoryId : this.userId.id
    }
    this.service.postApi('api/v1/admin/viewSubCategory',postData,'').subscribe((res)=>{
      if(res['responseCode']== 200){
        setTimeout(() => {
          this.service.hideSpinner() 
        }, 1000);
        this.user= res['result']
        this.subcategorypic = res['result']['image']
      }
      else{
        this.service.hideSpinner()
      }
    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong');
    })
  }


}
