import { Component, OnInit } from '@angular/core';
declare var $:any;
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';


@Component({
  selector: 'app-side-menu',
  templateUrl: './side-menu.component.html',
  styleUrls: ['./side-menu.component.css']
})
export class SideMenuComponent implements OnInit {
  isActive: any;
  subAdminId: any;
  user: any;
  permission: any;
  per: any;

  constructor( public router:Router,private service : DataService,private activatedRoute : ActivatedRoute) { }
   id:any;
   token:any;
   userType:any;
   flag:boolean=false;
  ngOnInit() {
    let url=window.location.href.split('/');
    this.isActive=url[3];
    console.log('flag',this.flag)
    //  console.log("current---=>",this.isActive)
  this.id=JSON.parse(localStorage.getItem('adminId'))
  // console.log(this.id)
  this.token=JSON.parse(localStorage.getItem('token'));
  $('.btn-toggle,.close_panel').click(() => {
    $('body').toggleClass('toggle-wrapper');
  });
  this.userType= window.atob(JSON.parse(localStorage.getItem('userType'))) 
  console.log('flag in function',this.flag,this.userType)
    this.subAdminList()
  if(this.userType=="SUBADMIN"){
    this.flag=true;
    console.log('flag in function',this.flag)
  }
  // console.log(this.id)
  }
  
 
  subAdminList(){
    let postData={
       subAdminId:this.id,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    } 
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewSubAdmin`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.user=response['result'];
        this.per = response['result']
        console.log("to check userDetails", this.permission)

      }
      else{
        this.service.hideSpinner()
      }
      
    },(err)=>{
      this.service.hideSpinner();
      this.service.toastErr('Something went wrong')
    })
  }

  

  logoutFunc(){
    $('#logout_modal').modal('hide');
    this.router.navigateByUrl('/login')
    localStorage.removeItem('adminInfo')
  }


  logoutModal() { 
    $('#logout_modal').modal({backdrop:"static",keyboard:false})
   
  }

  navigateTo(path){
    this.isActive = path
    this.router.navigateByUrl(path)
  }

  
}
