import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../services/data.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit-banner',
  templateUrl: './edit-banner.component.html',
  styleUrls: ['./edit-banner.component.css']
})
export class EditBannerComponent implements OnInit {
  userId: any=[];
  user: any;
  bannerpic: any;
  bannername: any;
  data: any = [];
  type :any;
  fileData: any;
  form:FormGroup;

  constructor(private activatedRoute :ActivatedRoute,private service : DataService,private router :Router) { }

                ngOnInit() {
                  this.getId();
                  this.viewBanner()
                this.form= new FormGroup({
                  'file' : new FormControl(''),
                  'bannername' : new FormControl('',([Validators.required,Validators.pattern(/^[^\s][A-Za-z\s]*$/)]))
                })
              }

           getId(){
             this.activatedRoute.params.subscribe((params)=>{
               this.userId = params
             })
           } 

           inputKeyPress(e){
            var k
            if (e.charCode == 32 && !e.target.value) {
              event.preventDefault()
              }
              else{
                document.all ? k = e.keyCode : k = e.which;
                return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
              }
          }

           handleFileInput(event) {
            var self = this;
            if(event.target.files && event.target.files[0]){
              var type = event.target.files[0].type;
            this.type=type
        
            if(type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
            var reader = new FileReader()
            reader.onload = (event:any)=> {
              if(type === 'image/png' || type === 'image/jpg'){
                self.fileData = event.target['result'].substring(22);
              } 
              else if (type === 'image/jpeg'){
                self.fileData = event.target['result'].substring(23);
              } 
              this.bannerpic = event.target['result']              
            }
            reader.readAsDataURL(event.target.files[0]);
             } else {
            this.service.toastErr("Select only jpg,jpeg and png file.");
            }
            }  
        
          }

        viewBanner(){
          let dummyData= {
            adminId : JSON.parse(localStorage.getItem('adminId')),
            bannerId :this.userId.id
          }
          this.service.showSpinner()
          this.service.postApi('api/v1/admin/viewBanner',dummyData,'').subscribe((res)=>{
            if(res['responseCode'] == 200){
              setTimeout(()=>{
                this.service.hideSpinner()
              },1000)
              this.bannername = res['result']['bannerName'];
              this.bannerpic = res['result']['image'];
              this.form.patchValue({
                bannername : this.bannername,
                bannerpic: this.bannerpic
              })
            }
          })

        }
         // 'data:'+this.type +'/;base64,'+this.fileData,
            

            editbanner(){ 
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
       bannerId : this.userId.id,
      bannerName : this.form.value.bannername,
      image  : this.bannerpic
     }
     this.service.showSpinner()
     this.service.postApi('api/v1/admin/editBanner',postData,'').subscribe((res)=>{
        if(res['responseCode'] == 200){
          setTimeout(()=>{
            this.service.hideSpinner()
  
          },1000)
          this.service.showSuccess('updated Succefully')
          this.router.navigate(['banner-management'])
        }
        else  {
          this.service.toastErr('retry again!')
          }
     })
     }
  

}
