import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
declare var $:any;
@Component({
  selector: 'app-job-transaction',
  templateUrl: './job-transaction.component.html',
  styleUrls: ['./job-transaction.component.css']
})
export class JobTransactionComponent implements OnInit {
  searchkey :any ="";
  user:any;
  pageNo: any = 1;
  paginationData: any;
  srNo: number;
  deleteId: any;
  status: string;
  userId: any;
  limit:any;
  total: any;
  Array: any = [];
  messege: any;
  form:FormGroup;
  totalPage: any;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  message: string;
  group: any=[];
  constructor(private service: DataService,private router : Router) { }

  ngOnInit() {
    this.form =  new FormGroup({
      "fromDate" : new FormControl(''),
        "toDate" : new FormControl('')
    });
    this.service.BlockFuture()
    this.search(this.pageNo)
  }
  search(page){
    this.pageNo = page
    let postData = {
       adminId : JSON.parse(localStorage.getItem('adminId')),
      pageNumber :this.pageNo,
      search : this.searchkey
    }
    this.service.postApi('api/v1/admin/jobTransaction',postData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.user = res['paymentData'];
        this.Array = res['paymentData']['docs']
        //  console.log("this.user=-=---=>",this.Array)
        this.paginationData = res['paymentData']
        this.limit = res['paymentData']['limit']
        this.total=res['paymentData']['total']
        this.pageNo = res['paymentData']['page']
        this.messege = res['responseMessage']
        this.totalPage = res['paymentData']['pages']
        this.srNo = (this.pageNo - 1) * this.paginationData.limit
      }
      else{
        this.messege = res['responseMessage']
      }
    },
    (error)=>{
      this.service.toastErr('something went wrong')

    })

  }

           // Search by Date
           searchByDate(){
            if(this.form.value.fromDate > this.form.value.toDate){
              this.service.toastErr('From date should be less  than todate')
              // this.form.value.fromDate = '';
              this.message = "please enter right date"
            }
            else if(this.form.value.fromDate <= this.form.value.toDate){

           this.oldDate = new Date(this.form.value.fromDate).toISOString()
              this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')

            let dummyDateData = {
              "adminId":JSON.parse(localStorage.getItem('adminId')) ,
             "fromDate": this.oldDate,
            "toDate":  this.newDate
            }
            this.service.postApi('api/v1/admin/jobTransaction',dummyDateData,'').subscribe((res)=>{
              if(res['responseCode'] == 200){
                this.user = res['paymentData'];
                this.limit = res['paymentData']['limit']
                this.total=res['paymentData']['total']
                this.pageNo = res['paymentData']['page']
                this.messege = res['responseMessage']
              }
              else{
                this.messege = res['responseMessage']

              }

            },(err)=>{
              this.service.toastErr('something went wrong')
            })
          }
        }

   Reset(){
    this.searchkey = '';
     this.form.reset()
      this.searchByDate()
 }

  view(id){
    console.log('this user id====>',id)
      this.router.navigate(['view-buying-transaction/'+id])
  }

  edit(id){
    //  console.log("Id=-=--=->",id);
     let user = this.Array.filter((item)=>item._id == id)
    //  console.log("User=--=-==--==-=->",user);
       this.service.setCategory(user)
       this.router.navigate(['edit-banner/'+id])
  }

  delete(id){
    this.userId = id
    $('#delete').modal('show');
    console.log('this user edit id====>',id)

  }

  deleteUser(){
    // this.delete(id)
    let postData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'paymentId' : this.userId,
      // "status":"DELETE"
    }
    console.log("delete user id ===>",this.userId)
   this.service.postApi('api/v1/admin/deleteJobTranasaction',postData,'').subscribe((res)=>{
        if(res['responseCode']==200){
          this.service.showSuccess(res['responseMessage'])
          $('#delete').modal('hide');
          this.search(this.pageNo)
       //location.reload()
        }
   }, (error)=>{
     this.service.toastErr('something went wrong')
   })
  }

  sliderRound(id,status)
  {
    this.status= status
    console.log(id,status)
    if(this.status=='ACTIVE'){

    this.status = "BLOCK"
    }
    $('#blocked').modal('show')
    console.log(id,status)
    console.log(id,this.status)
    this.userId = id;
    // var Status = status
    this.status = status;
  }
  blockCategory(id,status)
  {
    this.status= status
    if (this.status=='BLOCK'){
      this.status = "ACTIVE "
    }
    $('#activate').modal('show')

    // console.log(id,status)
    this.userId = id;
    this.status = status;
  }


  Activate(){
    this.sliderRound(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';
      }
      else{
        this.status = 'ACTIVE';
      }
    console.log(this.status)


    let apireq = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'bannerId':this.userId,
      'status': this.status,
      }
      // console.log('apireq ==>>', apireq)
    this.service.postApi('api/v1/admin/deleteBanner',apireq,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#blocked').modal('hide')
          this.service.toastErr(success['responseMessage'])
        this.search(this.pageNo)
         }
  },error => {
    console.log('Something went wrong');
  })

}

  Block(){
    this.blockCategory(this.userId,this.status)
    console.log('this is sthe id and status',this.userId,this.status)
    if(this.status=='ACTIVE'){
      this.status = 'BLOCK';
      }
      else{
        this.status = 'ACTIVE';
      }
    console.log(this.status)
    let req = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      'bannerId':this.userId,
      'status': this.status,
      }
    this.service.postApi('api/v1/admin/deleteBanner',req,'').subscribe((success:any)=> {
      console.log(success)
      if (success.responseCode === 200) {
        $('#activate').modal('hide')
          this.service.showSuccess(success['responseMessage'])
        this.search(this.pageNo)
    }
  },error => {
    console.log('Something went wrong');
  })
  }
}
