import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-product-management',
  templateUrl: './view-product-management.component.html',
  styleUrls: ['./view-product-management.component.css']
})
export class ViewProductManagementComponent implements OnInit {
id:any;
  productDetails: any;
  constructor(private service : DataService,private router : Router,private activatedroute : ActivatedRoute) { }

  ngOnInit() {
    this.activatedroute.params.subscribe((params)=>{
    this.id = params.id
    console.log('this is id of view Product',this.id)
    });
    this.viewProduct()
  }

  viewProduct(){
    let data = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      productId : this.id
    }
      this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewProduct',data,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.service.hideSpinner()
        this.productDetails = res['result']['docs'][0]
        console.log(this.productDetails)
      }
      else{
        this.service.hideSpinner()
      }
    })
  }

}
