import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from "@angular/forms";
// import { CompileShallowModuleMetadata } from '@angular/compiler';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  resetPasswordForm: FormGroup;
  id: any;
  queryString: any;

  constructor(public router: Router,public dataService: DataService,
    public activatedRoute: ActivatedRoute,public route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.buildResetPasswordForm();
    this.activatedRoute.params.subscribe((params)=>{
      this.queryString = params.id

    })
    console.log("Query string", this.queryString);
  }
  buildResetPasswordForm() {
    this.resetPasswordForm = new FormGroup({
      "password": new FormControl('', ([Validators.required, Validators.minLength(8), Validators.pattern(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,16}$/)])),
      "confirmPassword": new FormControl('', ([Validators.required])),
    });
  }


  onReset() {
    let resetData = {
      "adminId": this.queryString,
      "password": this.resetPasswordForm.value.password,
      "confirmPassword": this.resetPasswordForm.value.confirmPassword
    }
    console.log("47===========", resetData)
    this.dataService.postApi('api/v1/admin/resetPassword', resetData, "").subscribe(response => {
      console.log("in success>>>>>", response)
      if (response['responseCode'] == 200) {
        this.dataService.showSuccess(response['responseMessage'])
        this.router.navigate(['/login'])
      }
      else {
        this.dataService.toastErr(response['responseMessage'])
      }
    }, error => {
      this.dataService.toastErr('Something Went Wrong')
    })
  }

}
