import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-sticker',
  templateUrl: './view-sticker.component.html',
  styleUrls: ['./view-sticker.component.css']
})
export class ViewStickerComponent implements OnInit {
  userId: any;
  user:any;
  stickerPic : any;

  constructor( private activatedRoute : ActivatedRoute ,private service : DataService ) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
  }

  getId(){
    this.activatedRoute.params.subscribe((params)=> {
     this.userId = params
    })
  }
  
  viewUser(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     stickerId : this.userId.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewSticker',postData,'').subscribe((res)=>{
      if(res['responseCode']== 200){
        setTimeout(() => {
          this.service.hideSpinner()
        }, 1000);
        this.user= res['stickerData']
        this.stickerPic = res['stickerData']['image']

      }
    })
  }

}
