import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';
import { NotificationComponent } from '../notification/notification.component';
declare var $:any

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})

export class HeaderComponent implements OnInit {
token:any ;
profileimage: any;
count: any;
// public isCollapsed = false;
  constructor(private router : Router,private service:DataService) { 
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
      setTimeout(() => {
        this.count =  this.service.count; 
        console.log('count',this.count);
      
      }, 2000);
      let count =  this.service.getNotificationList(); 
      console.log('count',count)

  }

  ngOnInit() {
    this.token = JSON.parse(localStorage.getItem('token'))
     this.profileimage = "assets/img/profile.png"
    setTimeout(() => {
      this.getProfile()
    }, 2000);
  }

  notify(){
    alert('This is your notification')
  }

  getProfile(){
    let item = {
      adminId: JSON.parse(localStorage.getItem("adminId"))

     }
    this.service.postApi('api/v1/admin/adminProfile',item,'').subscribe((res)=>{
     this.profileimage = res['result']
     console.log("This is the admin profile pic",this.profileimage)
    })
  }
logoutModal(){
$('#logout_modal').modal('show')
}
  
logout(){
  localStorage.removeItem("token");
  this.router.navigate(['login'])
  this.token = null
  
}
login(){
  this.token = JSON.parse(localStorage.getItem('token'))

}

}
