import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';
declare var $:any;
@Component({
  selector: 'app-page-management',
  templateUrl: './page-management.component.html',
  styleUrls: ['./page-management.component.css']
})
export class PageManagementComponent implements OnInit {

  pageNo:any=1;
  group:any=[];
  srNo:Number=0;
  searchkey:any = '';
  paginationData:any;
  deleteId:any;
  status:any;
  groupId:any;
  limit:any;
  total:any;
  userId: any;
  message:any;
  form:FormGroup;
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;


  constructor(public router:Router , public service : DataService) { }

            ngOnInit() {
              this.form =  new FormGroup({
                "fromDate" : new FormControl(''),
                  "toDate" : new FormControl('')
              });
              this.service.BlockFuture();
              this.search(this.pageNo)
            }
            Reset(){
              this.searchkey = '';
               this.form.reset()
                this.searchByDate()
           }

            search(page){
              this.pageNo = page;
              let postData ={
                search: this.searchkey,
                pageNumber: this.pageNo,
                adminId : JSON.parse(localStorage.getItem('adminId'))
              }
              this.service.postApi(`api/v1/admin/viewPage`,postData,'').subscribe(response=>{
                if(response['responseCode']==200){
                  this.group=response['result'];
                  this.message = response['responseMessage']
                  this.paginationData=response['result'];
                  this.total=response['result']['total'];
                  this.limit = response['result']['limit']
                  console.log("check sl no>>>>",this.paginationData ,this.pageNo,response)
                }else{
                  this.message = response['responseMessage']
                }
              },error=>{
                this.service.toastErr('Something Went Wrong')
              })
            }
 

             // Search by Date
     searchByDate(){
      if(this.form.value.fromDate > this.form.value.toDate){
        this.service.toastErr('From date should be less  than todate')
        // this.form.value.fromDate = '';
        this.message = "please enter right date"
      }
      else if(this.form.value.fromDate <= this.form.value.toDate){
      
     this.oldDate = new Date(this.form.value.fromDate).toISOString()
        this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')
      
      let dummyDateData = {
        "adminId":JSON.parse(localStorage.getItem('adminId')) ,
       "fromDate": this.oldDate,
      "toDate":  this.newDate
      }
      this.service.postApi('api/v1/admin/viewPage',dummyDateData,'').subscribe((res)=>{
        if(res['responseCode'] == 200){
          this.group = res['result'];
          this.message = res['responseMessage']
        }
        else{
          this.message = res['responseMessage']
   
        }
       
      })
    }
  }


            view(id){
              console.log(id)
              this.router.navigate(['view-page/'+id]);
            }

              sliderRound(id,status)
            {
            this.status= status
            console.log(id,status)
            if(this.status=='ACTIVE'){

            this.status = "BLOCK"  
            }
            $('#blocked').modal('show')
            console.log(id,status)
            console.log(id,this.status)
            this.userId = id;
            // var Status = status
            this.status = status;                              
            }
            blockCategory(id,status)
            {
            this.status= status
            if (this.status=='BLOCK'){
            this.status = "ACTIVE "
            }
            $('#activate').modal('show')

            // console.log(id,status)
            this.userId = id;
            this.status = status;                              
            }


            Block(){ 
            this.sliderRound(this.userId,this.status)
            if(this.status=='ACTIVE'){
            this.status ='BLOCK';    
            }
            else{
            this.status = 'ACTIVE';
            }    


            let apireq = {
            'adminId' : JSON.parse(localStorage.getItem('adminId')),
            'pageId':this.userId,
            'status': this.status,
            }
            // console.log('apireq ==>>', apireq)
            this.service.postApi('api/v1/admin/pageManagement',apireq,'').subscribe((success:any)=> {
           
            if (success.responseCode === 200) {
              this.service.toastErr(success['responseMessage'])                                         
            $('#blocked').modal('hide')
            // this.status = success['result']['docs']['status']
            this.search(this.pageNo)     
            }
            },error => {
            console.log('Something went wrong');
            })

            }

            Activate(){
            this.blockCategory(this.userId,this.status)
            if(this.status=='ACTIVE'){
            this.status = 'BLOCK';    
            }
            else{
            this.status = 'ACTIVE';
            }    
            let req = {
            adminId : JSON.parse(localStorage.getItem('adminId')),
            'pageId':this.userId,
            }
            this.service.postApi('api/v1/admin/pageManagement',req,'').subscribe((success:any)=> {
            if (success.responseCode === 200) {
              this.service.showSuccess(success['responseMessage'])                     
              $('#activate').modal('hide')  
            // this.status = success['result']['docs']['status']

            this.search(this.pageNo)     
            }
            },error => {
            console.log('Something went wrong');
            }) 
            }

            delete(id){
              this.deleteId = id;
              $('#delete').modal('show');
              console.log('userId>>>>>>>>>>>',this.deleteId )
            }
          
            deleteUser(){
              let postData={
                adminId : JSON.parse(localStorage.getItem('adminId')),
                pageId: this.deleteId,
                status: 'DELETE'
              }
              this.service.postApi(`api/v1/admin/pageManagement`,postData,'').subscribe(response=>{
                if(response['responseCode']==200){
                  $('#delete').modal('hide');
                  this.service.showSuccess(response['responseMessage'])
                  this.search(this.pageNo)
                }
                else{
                  this.service.toastErr('try again')
                }
              })
            }

}
