import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { DataService } from '../services/data.service';
import { Router } from '@angular/router';
declare var $:any;

@Component({
  selector: 'app-auction-order',
  templateUrl: './auction-order.component.html',
  styleUrls: ['./auction-order.component.css']
})
export class AuctionOrderComponent implements OnInit {
  ProducName: any;
  categoryName: any;
  SubcategoryName: any;
  SellerName: any;
  pageNo: any= 1;
  jobs: any=[];
  srNo:Number;
  searchkey: any = "";
  paginationData:any;
  deleteId:any;
  jobStatus:any;
  jobsId:any;
  limit:any;
  total:any;
  user: any=[];
  messege: any;
  searchUserName:any = '';
  searchJobId:any ='';
  status: string;
  userId: any;
  ArrayData: any;
  ProductId:any;
  selectedValue:any;
  form:FormGroup;
  orderStatus: any="";
  toDate: string | number | Date;
  newDate: string | number | Date;
  oldDate: string | number | Date;
  fromDate: string | number | Date;
  constructor(private service: DataService , private router: Router) {
    $('.btn-toggle,.close_panel').click(() => {
      $('body').toggleClass('toggle-wrapper');
    });
  }

  ngOnInit() {
    this.form = new FormGroup({
    orderStatus : new FormControl(''),
    fromDate : new FormControl(''),
    toDate : new FormControl(''),
  });
  this.service.BlockFuture();
   this.search(this.pageNo);
   }

  search(page) {
    this.pageNo = page;
    if (navigator.onLine) {
      let postData = {
        adminId : JSON.parse(localStorage.getItem('adminId')),
        pageNumber : this.pageNo,
        search : this.searchkey,
      }
      this.service.postApi('api/v1/admin/auctionOrderManagement',postData,'').subscribe((res)=>{
      if ( res['responseCode'] == 200) {

      this.user = res['results']['docs']
    // this.ArrayData = res['result1']['docs']
    console.log(this.user)
    this.paginationData = res['results']
    this.limit = res['results']['limit'];
     this.total=res['results']['total'];
     this.pageNo = res['results']['page']
    this.messege = res['responseMessage']
    // this.srNo = (this.pageNo - 1) * this.paginationData.limit
  }
  else{
    this.messege = res['responseMessage']
  }
  },
  (error)=>{
  this.service.toastErr('something went wrong')
  }

  )
    }
    else{
      this.service.toastErr('check your internet connection')

    }

}

// Search by Date
searchByDate(){
  if(this.form.value.fromDate > this.form.value.toDate){
    this.service.toastErr('From date should be less  than todate')
    // this.form.value.fromDate = '';
    this.messege = "please enter right date"
  }
  else if(this.form.value.fromDate <= this.form.value.toDate){

 this.oldDate = new Date(this.form.value.fromDate).toISOString()
    this.newDate = new Date(this.form.value.toDate+'T23:59:59.999Z')

  let dummyDateData = {
    "adminId":JSON.parse(localStorage.getItem('adminId')) ,
   "fromDate": this.oldDate,
  "toDate":  this.newDate
  }
  this.service.postApi('api/v1/admin/auctionOrderManagement',dummyDateData,'').subscribe((res)=>{
    if(res['responseCode'] == 200){
      this.user = res['results']['docs'];
      this.paginationData = res['results']
      this.limit = res['results']['limit']
       this.total=res['results']['total'];
       this.pageNo = res['results']['page']
      this.messege = res['responseMessage']
    }
    else{
      this.messege = res['responseMessage']

    }

  })
}
}

Reset(){
  this.searchkey= ''
  this.form.reset()
   this.searchByDate()
}
  view(id){
    console.log(id)
    this.router.navigate(['view-auction-order/'+id]);
  }

  edit(id){
    console.log(id)
    this.router.navigate(['edit-order/'+id])
  }
  delete(id){
    this.deleteId = id;
    $('#delete').modal('show');
    console.log('userId>>>>>>>>>>>',this.deleteId )
  }

  deleteUser(){
    let postData={
      adminId : JSON.parse(localStorage.getItem('adminId')),
      orderId: this.deleteId
    }
    this.service.postApi(`api/v1/admin/deleteAuctionOrder`,postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.service.showSuccess('Order deleted succefully')
        this.search(this.pageNo);
      }
      else{
        this.service.toastErr('try again')
      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    })
  }


  sliderRound(id,status)
  {
  this.status= status
  console.log(id,status)
  if(this.status=='ACTIVE'){

    this.status = "BLOCK"
  }
  $('#blocked').modal('show')
  console.log(id,status)
  console.log(id,this.status)
  this.userId = id;
  this.status = status;
  }
  blockCategory(id,status)
  {
  this.status= status
  if (this.status=='BLOCK'){
   this.status = "ACTIVE "
  }
  $('#activate').modal('show')

  // console.log(id,status)
  this.userId = id;
  this.status = status;
  }


  Block(){
  this.sliderRound(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
  this.status = 'BLOCK';
  }
  else{
  this.status = 'ACTIVE';
  }
  console.log(this.status)


  let apireq = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'orderId':this.userId,
  'status': this.status,
  }
   console.log('apireq ==>>', apireq)
  this.service.postApi('api/v1/admin/deleteOrder',apireq,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#blocked').modal('hide')
    this.service.toastErr('Order blocked successfully')
  this.search(this.pageNo)
  }
  },error => {
  console.log('Something went wrong');
  })

  }

  Activate(){
  this.blockCategory(this.userId,this.status)
  console.log('this is sthe id and status',this.userId,this.status)
  if(this.status=='ACTIVE'){
  this.status = 'BLOCK';
  }
  else{
  this.status = 'ACTIVE';
  }
  console.log(this.status)
  let req = {
  adminId : JSON.parse(localStorage.getItem('adminId')),
  'orderId':this.userId,
  'status' : this.status
  }
  this.service.postApi('api/v1/admin/deleteOrder',req,'').subscribe((success:any)=> {
  console.log(success)
  if (success.responseCode === 200) {
  $('#activate').modal('hide')
    this.service.showSuccess('Order activated successfully')
  this.search(this.pageNo)
  }
  },error => {
  console.log('Something went wrong');
  })
  }

  ViewApplicant(id){
   let ApplicantId = id
   console.log('id',ApplicantId)
  this.router.navigate(['view-applicant/'+id]);

  }

}
