import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-auction-subcategory',
  templateUrl: './view-auction-subcategory.component.html',
  styleUrls: ['./view-auction-subcategory.component.css']
})
export class ViewAuctionSubcategoryComponent implements OnInit {
  userId :any;
  user:any;
  subcategorypic :any = " ";
  message: any;
  constructor(private activatedRoute : ActivatedRoute,private service : DataService) { }

  ngOnInit() {
    this.getId()
    this.viewUser()
  }

  getId(){
    this.activatedRoute.params.subscribe((params)=> {
     this.userId = params
      })
     }
  
  viewUser(){
    let postData = {
     adminId : JSON.parse(localStorage.getItem('adminId')),
     subCategoryAuctionId : this.userId.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewAuctionSubCategory',postData,'').subscribe((res)=>{
      if(res['responseCode']== 200){
        setTimeout(() => {
          this.service.hideSpinner()
        }, 1000);
        this.user= res['result']
        this.subcategorypic = res['result']['image']
      }
      else{
        console.log('else part of responseCode',res['responseCode']);
        this.message = res['responseMessage'];
      }
    },(err)=>{
      this.service.hideSpinner()
    })
  }
}
