import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-applicant',
  templateUrl: './view-applicant.component.html',
  styleUrls: ['./view-applicant.component.css']
})
export class ViewApplicantComponent implements OnInit {
  id:any;
  user:any;
  profilePic: any;
  image: string;
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.viewApplicant();
    this.image = 'assets/img/profile.png'
  }
  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.id=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.id,"KKKKKKKKKK",params)
    })
  }
  
  viewApplicant(){
    let postData={
      userId:this.id,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    } 
    this.service.postApi(`api/v1/admin/viewApplicant`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.user=response['result'];
        this.profilePic = response['result']['profilePic']
        console.log("to check userDetails", this.user)
      }
    })
  }

}
