import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-add-auction-subcategory',
  templateUrl: './add-auction-subcategory.component.html',
  styleUrls: ['./add-auction-subcategory.component.css']
})
export class AddAuctionSubcategoryComponent implements OnInit {
  user: any
  selectedValue: any = null;
  fileToUpload: File = null;
  obj: any = {};
  form: FormGroup;
  base64Image: any;
  upload: any;
  image: any;
  type: any;
  fileData: any;
  imageUrl: any = '';
  data: any;
  categoryname: any;
  subCategoryName: any = [];
  item: any = [];
  newItem: any = [];
  imageName: any;

  constructor(private service: DataService, private fb: FormBuilder, private router: Router) { }

  ngOnInit() {
    this.form = this.fb.group({
      "file": new FormControl('', Validators.required),
      "subCatName": new FormControl('', Validators.compose([Validators.required, Validators.pattern(/^[^\s][A-Za-z\s]*$/)])),
      "selectedValue": new FormControl('', Validators.required)
    });
    this.imageUrl = "assets/img/profile.png"
    this.categoryData()
  }

  // Block special character and numbers
  inputKeyPress(e) {
    var k
    if (e.charCode == 32 && !e.target.value) {
      event.preventDefault()
    }
    else {
      document.all ? k = e.keyCode : k = e.which;
      return ((k > 64 && k < 91) || (k > 96 && k < 123) || k == 8 || k == 32);
    }
  }


  //Image converted into base64 format
  handleFileInput(event) {
    var self = this;
    if (event.target.files && event.target.files[0]) {
      var type = event.target.files[0].type;
      this.type = type
      if (type === 'image/png' || type === 'image/jpg' || type === 'image/jpeg') {
        var reader = new FileReader()
        reader.onload = (e: any) => {
          if (type === 'image/png' || type === 'image/jpg') {
            self.fileData = e.target['result'].substring(22);
          }

          else if (type === 'image/jpeg') {
            self.fileData = e.target['result'].substring(23);
          }
          this.imageUrl = e.target['result']
          console.log(e.target['result'])

        }
        reader.readAsDataURL(event.target.files[0]);
        // this.imageName = event.target.files[0].name
        // // console.log(this.imageName)

      } else {
        this.service.toastErr("Select only jpg,jpeg and png file.");
      }
    }

  }

  uploadFile() {
    let postdata = {
      "adminId": JSON.parse(localStorage.getItem('adminId')),
      "auctionSubCategoryName": this.form.value.subCatName,
      "image": this.imageUrl,
      "auctionCategoryId": this.form.value.selectedValue,
      "categoryType": "SUBCATEGORY",
    }

    // console.log('newDAta','data:'+this.type +'/;base64,'+this.fileData)
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/addSubAuctionCategory', postdata, '').subscribe((res) => {
      console.log('subCategory response..', res)

      if (res['responseCode'] === 200) {
        this.service.hideSpinner()
        this.service.showSuccess('upload Successfully')
        this.router.navigate(['auction-subcategory-management'])
      }

      else {
        this.service.toastErr(res['responseMessage'])
        this.service.hideSpinner()
      }
    }, (err) => {
      this.service.toastErr('Please try again')
    })

  }


  categoryData() {
    let apireq = {
      "adminId": JSON.parse(localStorage.getItem('adminId')),
    }
    this.service.postApi('api/v1/admin/viewAuctionCategory', apireq, '').subscribe((res) => {
      console.log('category data response..', res)
      if (res['responseCode'] == 200) {
        this.item = res['result']['docs']
        // this.service.showSuccess(res['responseMessege'])
      }
      else {
        this.service.toastErr(res['responseMessege'])
      }
    })
  }


}
