import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-page',
  templateUrl: './view-page.component.html',
  styleUrls: ['./view-page.component.css']
})
export class ViewPageComponent implements OnInit {

  grouId:any;
  user:any=[];
  Posts: any;
  totalImages: any;
  totalVideo: any;
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.GetData();
  }
  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.grouId=params.id
    })
  }
  GetData(){
    let postData={
      pageId:this.grouId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.showSpinner()
    this.service.postApi(`api/v1/admin/viewPage`, postData,'').subscribe(response=>{
      if(response['responseCode']==200){
        this.service.hideSpinner()
        this.user=response['result'];
        this.Posts = response['allPost'];
        this.totalImages = this.Posts.reduce((rx, i) => rx + i.image.length, 0);
        this.totalVideo =this.Posts.reduce((rx, i) => rx + i.video.length, 0)

      }
    })
  }

}

