import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-view-class',
  templateUrl: './view-class.component.html',
  styleUrls: ['./view-class.component.css']
})
export class ViewClassComponent implements OnInit {

  classId:any;
  user:any;
  memberName:any;
  Posts: any;
  totalImages: any;
  totalVideo: any;
  constructor(public activatedRoute :ActivatedRoute , public service : DataService) { }

  ngOnInit() {
    this.getId();
    this.search();
  }

  getId(){
    this.activatedRoute.params.subscribe(params=>{
      this.classId=params.id
      console.log(">>>>>>>>>>>>>>>>>>>>",this.classId,"KKKKKKKKKK",params)
    })
  }
  search(){
    let postData={
      classId:this.classId,
      adminId : JSON.parse(localStorage.getItem('adminId'))
    }
    this.service.postApi(`api/v1/admin/viewClass`, postData,'').subscribe((response):any=>{
      if(response['responseCode'] == 200){
        this.user=response['result'];
        this.Posts = response['allPost'];
        this.totalImages = this.Posts.reduce((rx, i) => rx + i.image.length, 0);
        this.totalVideo =this.Posts.reduce((rx, i) => rx + i.video.length, 0)
         this.memberName=this.user['members'][0]['memberId'];
        console.log("ghdastfuilasdgjkfhgbdfh",this.memberName)
        console.log("to check userDetails", this.user)
      }
    })
  }

}
