import { Component, OnInit } from '@angular/core';
import { DataService } from '../services/data.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-all-report',
  templateUrl: './view-all-report.component.html',
  styleUrls: ['./view-all-report.component.css']
})
export class ViewAllReportComponent implements OnInit {
  id: any;
  data: any;
  message: any;

  constructor(private service : DataService,private router: Router,private activatedrouter: ActivatedRoute) { }

  ngOnInit() {
    this.activatedrouter.params.subscribe((params)=>{
     this.id = params.id
     console.log('id of report data',this.id)
    });
    this.viewReportData()
  }

  viewReportData(){
    let reportData = {
      adminId : JSON.parse(localStorage.getItem('adminId')),
      reportId : this.id
    }
    this.service.showSpinner()
    this.service.postApi('api/v1/admin/viewAllReport',reportData,'').subscribe((res)=>{
      if(res['responseCode'] == 200){
        this.data = res['result'];
        this.service.hideSpinner()
        this.message = res['responseMessage']
      }
      else{
        this.message = res['responseMessage']

      }
    },(err)=>{
      this.service.toastErr('Something went wrong')
    }
    )
  }
}
